.detail-container {
  max-width: 200px;
  font-family: AkkuratStd-Bold;
  color: $snw-darker-gray;

  ul {
    list-style-type: none;
    padding: 0;
  }

  div {
    display: block;
    font-size: 13px;
    @include truncate();
  }

  .name {
    color: $snw-black;
    font-weight: bold;
  }

  .website {
    a {
      text-decoration: none;
      color: $snw-blue;
    }
  }
}
