.snw-popup {
  @include center-element();
  .snw-message {
    @include transition(opacity .3s);
    display: none;
    font-size: 11px;
    text-align: justify;
    position: absolute;
    background-color: #9a9a9a;
    color: white;
    width: 200px;
    padding: 10px;
    left: 35px;
    z-index: 10;
    font-size: 10px;
    line-height: 1.5em;
    &:before {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      border-top: 7px solid transparent;
      border-bottom: 7px solid transparent;
      border-right: 7px solid #9a9a9a;
      z-index: 1000;
      left: -5px;
      top: calc(50% - 7px);
    }
  }
}
