.snw-summary {
  margin: 0;
  position: relative;
  display: flex;
  flex-flow: row wrap;
  .address {
    cursor: default;
    width: 80%;
    display: inline-block;
    padding: 10px;
    color: #666;
    p {
      font-size: 0.8em;
      line-height: 0.8em;
    }
  }
  .snw-street-view,
  .snw-favourite {
    cursor: pointer;
    // float: right;
    width: 10%;
    padding: 5px;
    .swn-rdg-icon-street-view {
      display: inline-block;
      background-size: contain;
      font-family: 'pegman';
      font-style: normal;
      font-size: 28px;
      &:before {
        content: "\e900";
      }
    }
    .snw-icon-star {
      height: 28px;
      width: 24px;
      display: inline-block;
      font-size: 28px;
    }
    p {
      text-align: center;
      font-size: 0.7em;
    }
  }
  .walking-time {
    border-top: 1px solid $gray-lighter;
    padding: 5px;
    color: $gray;
    font-size: 0.8em;
    width: 100%;
    display: flex;
    .walking-time-row {
      width: 100%;
      display: flex;
      justify-content: space-between;
      .walking-time-cell {
        &.walking-time-text {
          width: 70%;
        }
      }
    }
    .walking-time-see-route {
      display: flex;
      align-items: center;
      justify-content: flex-end;
      width: 30%;
      .snw-arrow-right {
        -ms-transform: rotate(90deg); /* IE 9 */
        -webkit-transform: rotate(90deg); /* Safari and Chrome */
        -o-transform: rotate(90deg); /* Opera */
        -moz-transform: rotate(90deg); /* Firefox */
        transform: rotate(90deg);
        color: inherit;
        width: 25px;
        font-size: 22px;
        position: relative;
        top: 1px;
        left: 2px;
      }
    }
    .snw-icon-walking {
      position: relative;
      top: 3px;
    }
    .snw-cta-see-map,
    .snw-cta-see-route {
      display: none;
      width: auto;
      position: relative;
      line-height: 1.7em;
      font-weight: 100;
    }
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-summary-collapse {
    .address {
      display: none;
    }
    .walking-time {
      .snw-cta-see-route {
        display: none;
      }
    }
    .swn-rdg-icon-street-view {
      height: 28px;
      width: 24px;
      display: inline-block;
      background-size: contain;
      background-image: url('../images/icons1x/swn-rdg-icon-street-view.png');
    }
  }
  .walking-time {
    .snw-cta-see-route {
      display: flex;
      cursor: pointer;
      justify-content: flex-end;
    }
  }
}

.map-container {
  .gm-iv-address,
  .gm-iv-address-description,
  .gm-iv-marker,
  .gm-compass * {
    box-sizing: content-box;
  }
  .gm-style {
    .gm-tracking-on {
      right: 9px !important;
    }
  }
}