.list-detail-container {
  color: #444444;

  .gallery-container {
    height: 155px;
    width: 100%;
  }

  .point-title {
    background-color: #ECECEC;
    padding: 0 15px 5px;

    .name {
      font-size: 15px;
      line-height: 34px;
      font-weight: bold;
      @include truncate();
    }

    .type {
      color: #b5b5b5;
      line-height: 12px;

      img {
        width: 12px;
        height: 12px;
        vertical-align: middle;
      }

      .type-name {
        color: #7b7b7b;
        font-size: 12px;
        font-weight: 400;
        vertical-align: middle;
        margin-left: 5px;
      }
    }
  }

  .margin-button {
    padding-top: 25px;
  }

  .icon {
    height: 30px;
    width: 30px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: center;
    margin-left: 15px;
    @include opacity(0.6);
  }

  .text {
    line-height: 34px;
    vertical-align: bottom;
    @include truncate();
  }

  .contact-details {
    border-top: 1px solid #eeecec;

    div {
      height: 34px;
    }

    .address-icon {
      background-image: url('../images/address.gif');
    }

    @each $icon in website,
    phone {
      .#{$icon}-icon {
        background-image: url('../images/#{$icon}.png');
      }
    }

    .icon-text-date,
    .icon-text-time {
      display: inline-block;
      width: 120px;
    }

    .from-style {
      margin: 0 5px 0 0;
      font-weight: bold;
    }

    .to-style {
      margin: 0 5px;
      font-weight: bold;
    }

    a:active,
    a:hover,
    a:link,
    a:visited {
      color: #1AB7F5;
      text-decoration: none;
      @include truncate();
      width: calc(100% - 60px);
      display: inline-block;
    }

    .text {
      @include truncate();
      width: calc(100% - 60px);
      display: inline-block;
    }
  }

  .street-view-container {
    height: 160px;
    width: auto;
    padding: 12px;
    border-top: 1px solid #eeecec;

    &.no-data {
      height: auto;
    }
  }

  .description {
    overflow: hidden;
    border-top: 1px solid #eeecec;
    margin: 12px;
    text-align: justify;
    padding-top: 12px;
    margin: 0 12px 12px;

    pre {
      white-space: pre-wrap;
      white-space: -moz-pre-wrap !important;
      white-space: -pre-wrap;
      white-space: -o-pre-wrap;
      word-wrap: break-word;
      font-size: 11px;
      font-family: WalesSansBody;
      line-height: 1.5em;
    }

    .description-title {
      font-weight: bold;
    }
  }
}

&[max-width='720px'] {
  .list-detail-container {
    .gallery-container {
      height: 250px;
    }

    .street-view-container {
      height: 160px;
    }
  }
}
