@font-face {
  font-family: 'snw-icomoon';
  src: url('../fonts/icomoon.eot?cndza3');
  src: url('../fonts/icomoon.eot?cndza3#iefix') format('embedded-opentype'), url('../fonts/icomoon.ttf?cndza3') format('truetype'), url('../fonts/icomoon.woff?cndza3') format('woff'), url('../fonts/icomoon.svg?cndza3#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: 'snw-material-icons';
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/MaterialIcons-Regular.eot");
  /* For IE6-8 */
  src: url("../fonts/MaterialIcons-Regular.woff2") format('woff2'), url("../fonts/MaterialIcons-Regular.woff") format('woff'), url("../fonts/MaterialIcons-Regular.ttf") format('truetype');
}

.snw-icon,
[class*="snw-icon-"],
[class^="snw-icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'snw-icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: $snw-line-height;
  font-size: $snw-icon-font-size;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; // @include center-element();
  display: inline-block;
  overflow: hidden;
  background-repeat: no-repeat;
  background-position: center;
  background-size: 100%;
}

.snw-md-icon,
[class*="snw-md-icon-"],
[class^="snw-md-icon-"] {
  font-family: 'snw-material-icons' !important;
  font-weight: normal;
  font-style: normal;
  font-size: $snw-icon-font-size;
  /* Preferred icon size */
  display: inline-block;
  line-height: $snw-line-height;
  text-transform: none;
  letter-spacing: normal;
  word-wrap: normal;
  white-space: nowrap;
  direction: ltr;
  /* Support for all WebKit browsers. */
  -webkit-font-smoothing: antialiased;
  /* Support for Safari and Chrome. */
  text-rendering: optimizeLegibility;
  /* Support for Firefox. */
  -moz-osx-font-smoothing: grayscale;
  /* Support for IE. */
  font-feature-settings: 'liga';
  color: #626363; //@include center-element();
}

