.snw-share-box {
  &-wrapper.snw-component-on-map {
    background-color: rgba(255, 255, 255, 0.92);
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    margin: 0;
    padding: 0 1em;
    .snw-close-button {
      padding: 0.5em 1em;
      position: absolute;
      top: 1em;
      right: 1em;
      font-size: 0.9em;
      border: 1px solid #999;
      border-radius: 5px;
      color: #999;
      cursor: pointer;
      .snw-icon-close,
      .snw-close-text {
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
  &-content {
    position: relative;
    display: block;
    margin-top: 4em;
    text-align: center;
    &>div,
    &>p {
      display: block;
      text-align: center;
      margin: 0 auto;
    }
    &>div.snw-share-box-copied-button {
      border: 1px solid #35B869;
      padding: 0.5em 1em;
      display: inline-block !important;
      margin: 0 auto;
      border-radius: 5px;
      color: #555;
      cursor: default;
      .snw-md-icon-check {
        color: #35B869;
        top: 4px;
        position: relative;
      }
      visibility: hidden;
    }
    &>.snw-share-box-input {
      display: block;
      max-width: 450px;
      position: relative;
      height: 2.5em;
      margin: 1em auto;
      .snw-share-box-input-form {
        border: 1px solid #ddd;
        padding: 5px;
        border-radius: 5px;
        height: 100%;
        input {
          width: calc(100% - 110px);
          padding-right: 70px;
          margin-left: 90px;
          border: 0;
          outline: 0;
          background: transparent;
          height: 100%;
          cursor: text;
        }
      }
      .snw-share-box-input-copy-button {
        position: absolute;
        display: block;
        background-color: #24A9E3;
        padding: 0.5em 2em;
        border-radius: 0 5px 5px 0;
        top: 0;
        right: 0;
        height: 100%;
        color: #fff;
        font-size: 0.8em;
        line-height: 2em;
        cursor: pointer;
      }
      .snw-share-box-input-pre {
        display: block;
        background-color: #EBEBEB;
        padding: 0.5em 1em;
        font-size: 0.8em;
        text-transform: uppercase;
        color: #999;
        border-radius: 5px;
        max-width: 100px;
        text-align: center;
        position: absolute;
        height: calc(100% - 8px);
        left: 3px;
        top: 4px;
        cursor: default;
      }
    }
    &>.snw-share-box-title {
      margin: 2em auto 0;
      text-transform: uppercase;
      color: #999;
      padding: 2em 2em 0 2em;
      cursor: default;
      .snw-md-icon-share {
        font-size: 3em;
        margin-bottom: .3em;
        display: block;
        color: #999;
      }
      &>p {
        border-bottom: 2px solid #F5F5F4;
        display: inline-block;
        padding: 0 1em 1em 1em;
        cursor: default;
      }
    }
    .snw-share-box-done-button {
      padding: 0.5em 1.5em;
      display: inline-block!important;
      cursor: pointer;
      border: 1px solid #52B9E9;
      color: #52B9E9;
      background-color: transparent;
      border-radius: 5px;
      font-size: 0.8em;
    }
    &>p {
      font-size: 1.2em;
      color: #999;
      margin: 1em 0;
      cursor: default;
    }
  }
}

@media (max-width:$snw-break-mobile) {
  .snw-share-box-wrapper {
    overflow-y: auto;
  }
  .snw-share-box-content {
    margin-bottom: 20px;
    .snw-share-box-title {
      margin: 0;
      padding: 1em 2em 0 2em;
    }
  }
}