.autocomplete {
  width: 100%;
  .search-section {
    padding: 0.8em 0.8em 0.8em 0.2em;
    color: #666666;
    @include center-element(space-between);
    input {
      //  font-family: WalesSansBody;
      font-size: 1em;
      font-weight: 400;
      width: calc(100% - 40px);
      border: none;
      color: #737373;
      &:focus {
        outline: none;
      }
      &::-ms-clear {
        display: none;
      }
    }
    i {
      color: #91908F;
      width: 20px;
      font-size: 1.5em;
      margin-left: 0.5em;
      &.reset {
        font-size: 1.5em;
        cursor: pointer;
        border-left: 1px solid #91908F;
        padding-left: 8px;
        margin-left: 0.5em;
        display: none;
      }
    }
  }
  .error-search-panel {
    // font-family: WalesSansBody;
    border-top: 1px solid #efefef;
    padding: 10px;
    font-size: 1em;
    color: #666;
    display: none;
    position: absolute;
    z-index: 15;
    background-color: #fff;
    width: calc(100%);
    @include box-shadow();
    right: 0;
  }
  i {
    width: 20px;
    font-size: 1.5em;
  }
  .results {
    position: relative;
    top: -13px;
    max-height: calc(100% - 90px);
  }
}

.ui-autocomplete.ui-front.ui-menu.ui-widget.ui-widget-content {
  top: 0 !important;
  left: 0 !important;
  overflow-x: hidden;
  overflow-y: auto;
  position: absolute;
  width: 315px !important;
  padding: 0;
  background-color: #fff;
  list-style: none;
  font-size: 0.9em;
  color: #666666;
  border-bottom: 1px solid #efefef;
  z-index: 10;
  @include box-shadow();
  li {
    // font-family: WalesSansBody;
    padding: 10px 10px 10px 25px;
    border-top: 1px solid #efefef;
    cursor: pointer;
    &.ui-autocomplete-category,
    &.ui-autocomplete-loading {
      padding-left: 12px;
      font-weight: 600;
      span {
        /*   float: left; */
        width: inherit;
      }
      .loading-data {
        background-image: url('../images/loader.gif');
        background-repeat: no-repeat;
        background-position: center;
        width: 20px;
        height: 20px;
        margin-left: 6px;
        &.categorized {
          /*  float: left; */
          margin-left: 10px;
          display: inline-block;
        }
      }
    }
    &.ui-menu-item:hover,
    &.ui-state-focus {
      background-color: #E6E6E6;
    }
    i.autocomplete-ui-icon {
      /* padding-top: 5px; */
      line-height: 29px;
      font-size: 15px;
      float: left;
      &:before {
        background-repeat: no-repeat;
        background-size: contain;
        content: "";
        display: block;
        width: 29px;
        height: 29px;
        float: left;
        margin: 0 6px 0 -12px;
      }
      &.default:before {
        background-image: url('../images/06_GooglePlaces_default.png');
      }
    }
    span {
      display: inline-block;
      width: 100%;
      @include truncate();
    }
    i.autocomplete-ui-icon+span {
      padding-top: 5px;
      width: calc(100% - 40px);
    }
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .ui-autocomplete.ui-front.ui-menu.ui-widget.ui-widget-content {
    width: calc(100% - 20px) !important;
  }
}