.snw-selected-row {
  background-color: $snw-selected-row;
  padding: 0 !important;
  position: absolute;
  width: 100%;
  height: 37px;
  .snw-selected-content {
    width: 100%;
    color: $snw-title-font-color;
    cursor: default;
    white-space: nowrap;
    display: inline-block;
    height: 100%;
    span {
      height: 100%;
      padding: $snw-main-padding 5px !important;
      &.time {
        padding: $snw-main-padding 5px $snw-main-padding $snw-main-padding !important;
      }
    }
    .time,
    .destination,
    .estimated {
      float: left;
    }
    .destination {
      max-width: 28%;
      @include truncate();
    }
    .see-routes-btn,
    .close-selected-btn {
      cursor: pointer;
      float: right;
    }
    .see-routes-btn {
      border-radius: 0;
      position: relative;
      &:before {
        font-family: 'snw-material-icons' !important;
        content: '\e922';
        display: inline-block;
        -webkit-transform: rotate(90deg);
        -moz-transform: rotate(90deg);
        -o-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
        transform: rotate(90deg);
      }
    }
    .close-selected-btn {
      height: 20px;
      &:before {
        font-family: 'snw-material-icons' !important;
        content: '\e5cd';
        font-size: $snw-icon-size;
      }
    }
  }
}