.marker-selected-shown {
  display: block;
  position: relative;
  cursor: pointer;
}

.marker-selected-hidden {
  display: none;
}

.snw-geo-marker {
  cursor: pointer;
  text-align: center;
}

.cluster {
  text-align: center;
}

.snw-marker-label {
  left: -7px;
  color: #fff;
  line-height: 1.5;
  top: -20px;
  background-color: #026A51;
  border: none;
  font-size: 10px;
  border-radius: 3px;
  width: 40px;
  position: absolute;
  padding: 1px;
  text-align: center;
  &:after {
    content: '';
    width: 0;
    height: 0;
    border-right: 3px solid transparent;
    border-top: 4px solid #026A51;
    border-left: 3px solid transparent;
    bottom: -4px;
    position: absolute;
    left: calc(50% - 4px);
  }
}

.snw-marker-transit-stop {
  margin-top: 5px;
  width: 12px;
  height: 12px;
  background: white;
  border: 2px solid black;
  border-radius: 50%;
}