* {
  font-family: $snw-font-family;
  box-sizing: border-box;
  @include non-selectable();
}

input,
input:before,
input:after {
  -webkit-user-select: text;
  -khtml-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.snw-disabled {
  * {
    cursor: default!important;
    pointer-events: none;
    @include opacity(0.9);
  }
}

.snw-link {
  color: $snw-link-color;
  text-decoration: underline;
}

.snw-p {
  font-size: $snw-medium-title-size;
  margin-bottom: 2px;
}

.snw-bold {
  font-weight: 600;
}

.snw-small {
  font-size: $snw-small-title-size;
}

.snw-shown {
  display: block;
}

.snw-hide {
  display: none;
}

.snw-collapsed {
  height: 0;
}

.snw-title {
  width: 100%;
  height: 100%;
  font-size: $snw-medium-title-size;
  font-weight: $snw-title-weight;
  color: $snw-title-color;
}

.snw-panel-header {
  @include center-element(space-between);
  padding: 0 10px;
  height: 55px;
  line-height: 55px;
  background-color: #fff;
}

.snw-container {
  background-color: white;
  width: 100%;
  overflow: hidden;
  padding: $snw-container-padding;
}

.snw-checkbox {
  width: 20px;
  margin: 0;
  &:focus {
    outline: none;
    outline-offset: 0;
  }
}
