.snw-transport-mode-panel {
  display: none;

  .snw-transport-mode-header {
    display: inline-block;  
    span {
      position: absolute;
      left: 30px;
      font-size: 12px;
      cursor: default;
    }

    .snw-icon-close {
      position: absolute;
      right: 10px;
    }
  }

  .snw-transport-mode-icons {
    display: flex;
    margin-top: 20px;
    text-align: center;
    padding: 5px 25px 5px 25px;

    .snw-icon-transport {

      &:hover {
        opacity: 0.5;
      }
    }

    .snw-mode-selected {
      border-radius: 5px;

      &:hover {
        opacity: 0.5;
      }
    }

    i {
      width: 100%;
      font-size: 30px;
      text-align: center;
      margin: 0 auto;
    }
  }
}
