.snw-routes-options {
  height: 0;

  .snw-routes-options-panel {
    background-color: $snw-secondary-background;
    display: inline-block;
    width: 100%;
    height: 35px;

    .snw-options-mode-selector,
    .snw-options-more-options,
    .snw-options-time-selector,
    .snw-options-date-departure {
      opacity: 0;
      padding: $snw-main-padding;
      line-height: 1em;
      vertical-align: top;
      float: right;
      position: relative;

      .snw-disabled {
        i {
          opacity: 0.3;
        }
      }
    }

    .snw-options-date-departure {
      cursor: default;
      padding: $snw-main-padding;
      color: #878787;
    }

    .snw-options-mode-selector,
    .snw-options-more-options {
      bottom: 3px;
    }

    .snw-options-time-selector {
      bottom: 2px;
      float: left;
    }

    .snw-options-date-departure {
      float: left;
      opacity: 1;
    }
  }

  .snw-routes-options-container {
    border-bottom: 1px solid #d0d0d0;
    display: none;
    z-index: 2;
    background-color: $snw-secondary-background; //fixes de-327
    position: fixed; //fixes de-327
    top: 181px;
    width: $snw-component-width;
    padding: 10px;
  }
}
//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-routes-options {
    .snw-routes-options-container {
      width: 100%; //fixes de-327
      top: 149px;
    }
  }
}
