.marker-label.incident {
  color: #4e4e4e;
  background-color: #fff;
  border: none;
  font-size: 1.2em;
  border-radius: 3px;
  min-width: 100px;
  white-space: nowrap;
  height: 25px;
  position: absolute;
  padding: 0 3px;
  text-align: center;
  bottom: 31px;
  left: -31px;
  -webkit-filter: drop-shadow(0px 2px 2px rgba(130, 130, 130, 1));
  filter: drop-shadow(0px 2px 2px rgba(130, 130, 130, 1));
  -ms-filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX=0, OffY=2, Color='#444')";
  filter: "progid:DXImageTransform.Microsoft.Dropshadow(OffX=0, OffY=2, Color='#444')";
  box-shadow: 2px 2px 0 rgba(130, 130, 130, 1);
  &.incident {
    padding: 3px;
    .content {
      position: relative;
      top: 3px;
    }
  }
  &:after {
    content: '';
    width: 0;
    height: 0;
    border-right: 3px solid transparent;
    border-top: 4px solid #fff;
    border-left: 3px solid transparent;
    bottom: -4px;
    position: absolute;
    left: 15px;
  }
  .content {
    padding-bottom: 3px;
    span {
      position: relative;
      top: 4px;
    }
  }
}