.snw-directions-box {
  position: static!important;
  input {
    @include placeholder;
  }
  .snw-directions-box-header {
    position: relative;
    display: flex;
    background-color: $snw-component-background;
    height: 95px;
    .snw-directions-autocomplete {
      width: 100%;
    }
  }
  .snw-directions-header-list-routes {
    display: none;
    height: $snw-header-component-height;
    background-color: $snw-component-background;
    color: $snw-font-color;
    -webkit-box-shadow: $snw-box-shadow;
    -moz-box-shadow: $snw-box-shadow;
    box-shadow: $snw-box-shadow;
    .snw-service-title {
      margin-left: 35px;
      font-size: $snw-tabs-font-size;
      padding: 7px;
      width: calc(100% - #{$snw-icon-size});
      @include truncate();
      .snw-header-label {
        color: $snw-secondary-color;
      }
    }
    .snw-icon-back,
    .snw-icon-close {
      z-index: 11;
      position: absolute;
      right: 0;
      top: 0;
      padding: $snw-main-padding;
      cursor: pointer;
      &:before {
        width: $snw-icon-size;
        height: $snw-icon-size;
      }
    }
    .snw-icon-back {
      left: 0;
      color: $snw-title-font-color;
    }
  }
  width: $snw-component-width;
  font-size: 14px;
  border-radius: 2px;
  @include box-shadow();
  .snw-directions-box-input {
    @include center-element(space-between);
    height: 45px;
    .snw-directions-box-autocomplete {
      width: 100%;
      .results {
        left: -40px;
      }
      .snw-from-autocomplete {
        left: -40px;
        .search-section {
          &.error {
            border-bottom: 2px solid $snw-cancelled-color;
          }
          input {
            background-color: #fff;
          }
          height: 38px;
          border-bottom: 1px solid $component-border;
        }
      }
      .snw-to-autocomplete {
        left: -40px;
        .search-section {
          &.error {
            border-bottom: 2px solid $snw-cancelled-color;
          }
          input {
            background-color: #fff;
          }
          height: 30px;
          border-bottom: 1px solid $component-border;
        }
      }
    }
  }
  .snw-clickable {
    &.snw-icon-chevron-down,
    &.snw-icon-chevron-up,
    &.snw-md-icon-swap {
      color: $snw-secondary-color;
      font-weight: bold;
      cursor: pointer;
    }
  }
  .snw-directions-box-icon,
  .snw-directions-swap,
  .snw-panel-collapse {
    text-align: center;
    width: 40px;
    text-align: center;
    .snw-md-icon-circle {
      font-size: 15px;
      color: #b5b5b5;
      cursor: default;
    }
    .snw-md-icon-pin {
      color: #b5b5b5;
      cursor: default;
    }
  }
  .snw-directions-swap {
    position: relative;
    top: 40%;
  }
  .snw-md-icon-dots {
    position: absolute;
    top: calc(50% - 15px);
    left: 8px;
  }
  .snw-search {
    background-color: $snw-component-background;
    position: relative;
    padding: 0 40px $snw-main-padding;
    .snw-search-button {
      &.snw-clickable {
        cursor: pointer;
      }
      &:not(.snw-clickable) {
        opacity: 0.4;
      }
      border-radius: 2px;
      background-color: $snw-secondary-background;
      display: table;
      box-sizing: border-box;
      width: 100%;
      * {
        display: table-cell;
        padding: 5px;
        vertical-align: middle;
        &.snw-icon-search {
          text-align: right;
          width: 35%;
        }
        & span {
          text-align: left;
        }
      }
    }
  }
  .snw-directions-box-options-panel {
    height: 35px;
    position: relative;
  }
  .snw-autocomplete-results {
    margin-top: -40px;
    position: relative;
    background-color: $snw-component-background;
    z-index: 1;
    .autocomplete-results {
      ul {
        overflow-y: hidden;
        height: 100%;
        width: 100% !important;
      }
      li {
        border-top: none !important;
        .snw-railway-icon {
          background-image: url('../images/icons2x/snw-railway-icon.png');
          background-repeat: no-repeat;
          background-size: auto 15px;
          background-position: center;
        }
        .snw-station-result {
          padding: $snw-main-padding $snw-main-padding $snw-main-padding 0 !important;
          width: 100%;
          border-top: 1px solid #efefef;
          span {
            width: auto;
          }
        }
      }
    }
    .autocomplete-error {
      display: none;
      background-image: url('../images/icons2x/snw-icon-danger-message.png');
      background-repeat: no-repeat;
      background-size: auto 25px;
      background-position: 15px center;
      padding: $snw-main-padding;
      padding-left: 50px;
      box-shadow: 0px 1px 8px -3px black;
    }
  }
  .direction-box-fix-map-drag {
    height: 100%;
    .direction-box-transparent-overlay {
      pointer-events: none;
    }
    .snw-directions-box-custom-panel {
      position: absolute;
      max-height: calc(100vh - 250px - 40px);
      overflow: auto;
      width: 100%;
      z-index: 1;
      background-color: $snw-component-background;
    }
  }
  .snw-directions-box-routes-panel {
    display: none;
    max-height: calc(100vh - 220px);
    overflow: auto;
    width: $snw-component-width;
    z-index: 1;
    background-color: $snw-component-background;
    .snw-route {
      list-style: none;
    }
    .snw-service-details .snw-service-time-info .snw-service-duration {
      float: right;
    }
  }

  .snw-directions-box-disclaimer-message {
    position: relative;
    display: none;
    background-color: $snw-disclaimer-background;
    color: black;
    padding: 15px 30px 15px 50px;
    background-image: url('/images/icons2x/snw-icon-alert-message.png');
    background-size: auto 25px;
    background-repeat: no-repeat;
    background-position: 15px center;
    &[style="display: block;"] ~ .snw-directions-box-routes-panel {
      max-height: calc(100vh - 322px) !important;
    }
    span {
      font-size: 0.9em;
      vertical-align: middle;
    }
    .close {
      &:before {
        content: "\e93f";
      }
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 15px;
      color: #91908F;
      cursor: pointer;
    }
  }

  .snw-directions-box-back-to-original-route {
    display: none;
    background-color: $snw-secondary-hover-background;
    color: black;
    cursor: pointer;
    padding: 5px;
    border-bottom: 1px solid #DDD;
    &[style="display: block;"] ~ .snw-directions-box-routes-panel {
      max-height: calc(100vh - 246px);
    }
    span, i {
      vertical-align: middle;
    }
    i {
      font-size: 25px;
    }
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-directions-box {
    margin: 0;
    width: 100%;
    height: 100%;
    font-size: 16px;
    .snw-directions-box-header {
      height: 100px;
    }
    .snw-directions-box-routes-panel {
      max-height: calc(100% - 130px - 35px);
      width: 100%;
      z-index: 1;
    }
    .direction-box-fix-map-drag {
      height: 100%;
      .snw-directions-box-custom-panel {
        max-height: calc(100% - 125px - 40px);
      }
      .direction-box-transparent-overlay {
        height: 38%;
      }
      .snw-directions-box-routes-panel {
        height: calc(100% - 223px);
      }
    }
    .autocomplete-results {
      // DE085: allows scrolling the results in mobile
      position: fixed;
      top: 146px;
      bottom: 35px;
      left: 0px;
      .ui-autocomplete.ui-front.ui-menu.ui-widget.ui-widget-content {
        width: 100vw !important;
      }
    }
    .autocomplete-error {
      position: fixed;
      background-image: url('../images/icons1x/snw-icon-danger-message.png');
      top: 146px;
      left: 0px;
      background-color: white;
    }
    .snw-autocomplete-results .autocomplete-results .snw-railway-icon {
      background-image: url('../images/icons1x/snw-railway-icon.png');
    }
    .snw-directions-box-disclaimer-message {
      background-image: url('/images/icons1x/snw-icon-alert-message.png');
    }
  }
  .snw-journey-container-collapse {
    .snw-directions-box {
      display: none!important;
    }
  }
}
