.snw-panel-collapsible-header {
  cursor: pointer;
  &:hover {
    background-color: #e2e2e2;
  }
}

.snw-btn-collapse {
  width: 30px;
  height: 30px;
  @include center-element();
  @include transition(transform 0.3s);
}

.snw-rotate {
  @include transition(transform 0.3s);
  @include rotate(-90);
}

.snw-upper-container {
  overflow: hidden;
  @include transition(0.3s);
}