.snw-message-control {
  z-index: 1!important;

  .snw-message {
    background-color: #fff;
    @include center-element();
    @include box-shadow();
    width: 350px;
    padding: 10px;
    font-size: 12px;

    .snw-icon {
      margin-right: 10px;
      min-width: 24px;
    }

    .snw-message-text {
      width: 100%;
    }

    .snw-icon-close {
      cursor: pointer;
      cursor: hand;
    }
  }
}

&[max-width='720px'] {
  .snw-message-control {
    width: calc(100% - 20px);

    .snw-message {
      width: 100%;
    }
  }
}
