//Colors:
$snw-black: #333;
$snw-lighter-gray: #efefef;
$snw-gray: gray;
$snw-light-gray: #BDBDBD;
$snw-darker-gray: #4d4d4d;
$snw-blue: #0080BD;
$snw-light-blue: #29A6DF;
$snw-darker-blue: #046490;
//Typings
$snw-title-color: $snw-darker-gray;
$snw-big-title-size: 16px;
$snw-medium-title-size: 14px;
$snw-small-title-size: 10px;
$snw-title-weight: 600;
$snw-line-height: 1;
$snw-font-family: helvetica;
$snw-link-color: $snw-blue;
//Components
$snw-radius: 1px;
$snw-component-bg-color: white;
$snw-component-margin: 10px;
$snw-component-border: 0;
$snw-component-shadow-size: 1px;
$snw-component-shadow-color: $snw-light-gray;
//Container
$snw-container-padding: 5px;
//Main Menu
$snw-main-menu-bg-color: white;
$snw-main-menu-color: $snw-darker-gray;
$snw-main-menu-width: 315px;
$snw-main-menu-padding: 0;
$snw-main-menu-height-header: 50px;
$snw-main-menu-width--mobile: calc(100% - 20px);
$snw-main-menu-maxheight--mobile: calc(100% - 100px);
$snw-main-menu-maxheight: calc(100% - 125px);
//Tab Button
$snw-tab-btn-bg-color: white;
$snw-tab-btn-color: $snw-darker-gray;
$snw-tab-btn-font-size: $snw-medium-title-size;
$snw-tab-btn-color--selected: $snw-darker-gray;
$snw-tab-btn-bg-color--selected: $snw-lighter-gray;
//Lateral Panel
$snw-lateral-panel-bg-color: white;
$snw-lateral-panel-color: $snw-darker-gray;
$snw-lateral-panel-header-bg-color: $snw-lighter-gray;
//Lateral Button
$snw-lateral-menu-btn-bg-color: white;
$snw-lateral-menu-size: 40px;
$snw-lateral-menu-icon-size: 28px;
//Toggle Button
$snw-toggle-btn-size: 40px;
//Component-Btn
$snw-btn-size: 27px;
$snw-btn-bg-color: white;
$snw-btn-color: $snw-darker-gray;
$snw-btn-border: none;
//List
$snw-list-color: $snw-gray;
$snw-list-font-size: $snw-medium-title-size;
$snw-list-color--active: $snw-darker-gray;
$snw-list-icon-color: $snw-gray;
$snw-list-icon-color--active: $snw-darker-gray;
$snw-list-bg-color: white;
$snw-list-bg-color--hover: $snw-lighter-gray;
$snw-list-padding: 5px;
$snw-list-border: none;
//Icons
$snw-icon-font-size: 20px;
//Toggle
$snw-toggle-ball-bg-color: white;
$snw-toggle-ball-size: 13px;
$snw-toggle-ball-bg-color--active: white;
$snw-toggle-bar-bg-color: $snw-light-gray;
$snw-toggle-bar-bg-color--active: $snw-gray;
$snw-toggle-bar-left--active: 15px;
$snw-toggle-bar-height: 15px;
$snw-toggle-bar-width: 30px;
$snw-toggle-bar-top: 4px;
$snw-toggle-ball-left: 1px;
//Travel Time View
$snw-traveltime-bg-color: white;
$snw-traveltime-tab-color: $snw-light-gray;
$snw-traveltime-tab-color--active: $snw-darker-gray;
$snw-traveltime-tab-color--hover: $snw-darker-gray;
$snw-traveltime-tab-bg-color: white;
$snw-traveltime-tab-bg-color--active: $snw-light-gray;
$snw-traveltime-tab-bg-color--hover: $snw-light-gray;
$snw-traveltime-tab-size: 25px;
$snw-traveltime-padding: 5px;
$snw-traveltime-slider-from-color: green;
$snw-traveltime-slider-to-color: blue;
$snw-traveltime-slider-handle-color: green;
$snw-traveltime-slider-handle-from-color: #278783;
$snw-traveltime-slider-handle-to-color: #235d85;
$snw-traveltime-slider-label-bg-color: $snw-darker-gray;
$snw-traveltime-slider-label-color: #fff;
//Travel Time Component
$snw-traveltime-header-height: 55px;
$snw-traveltime-header-bg-color: #fff;
$snw-traveltime-header-size: 12px;
$snw-traveltime-header-weight: 300;
$snw-traveltime-header-color: $snw-title-color;
$snw-traveltime-slider-value: #404040;
$snw-traveltime-slider-disabled: #c5c5c5;
$snw-traveltime-slider-disabled-handle: #f6f6f6;
$snw-traveltime-checkbox-bg--checked: $snw-gray;
$snw-traveltime-checkbox-bg: white;
$snw-traveltime-checkbox-size: 19px;
$snw-traveltime-checkbox-border-radius: 2px;
$snw-traveltime-checkbox-bg-image: none;
$snw-traveltime-checkbox-bg-image--checked: url('../images/check-white.png');
$icon-no-clickable: #a2a2a2;