 .loading-warning {
   background-color: white;
   border-left: 1px solid #eeecec;
   @include box-shadow();
   font-family: WalesSansBody;
   color: #454545;
   padding: 10px 8px;
   opacity: 0.9;
   .loading-text {
     line-height: 20px;
     padding-left: 5px;
   }
   .loading-data {
     background-image: url('../images/loader.gif');
     background-repeat: no-repeat;
     background-position: center;
     width: 20px;
     height: 20px;
     float: left;
     margin-left: 10px;
   }
 }