.google-place-search {
    width: 250px;
    border: 0;
    background-color: #fff;
    .search-section{
      padding: 12px;
      @include center-element();
      input{
          background-color: #fff;
          position: relative;
          width: 90%;
          border: none;
          &:focus {
              outline: none;
          }
      }
      i {
        width: 10%;
        font-size: 20px;
        color: hsl(0, 0%, 50%);
        font-weight: 500;
      }
    }
}
