$snw-service-line-circle-size: 16px;
$snw-service-step-height: 40px;
$snw-service-step-circle-top: 2px;
$snw-service-point-inside-size: 5px;
$snw-train-icon--size: 40px;
$snw-train-icon--margin-top: -15px; // It should be $snw-train-icon-size/2 but that doesn't work ok :/
.snw-service-steps {
  margin: 0;
  padding: 15px;
  padding-top: 30px;
  box-shadow: none;
  height: auto;
  overflow-y: auto;
  position: relative; // So train icon can be positioned absolutely

  .snw-service-step {
    font-size: $font-size-base;
    min-height: $snw-service-step-height;
    display: flex;
    &:last-child() {
      height: 25px;
      .snw-service-step-line {
        height: 10px;
        min-height: 10px;
      }
    }
    .snw-service-step-time {
      text-align: center;
      width: 95px;
      margin-top: -$snw-service-step-circle-top;
      margin-right: 7px;
      cursor: default;
      .snw-service-step-time-real {
        margin-top: 5px;
        margin-bottom: 15px;
        &.ontime {
          color: $snw-ontime-color;
        }
        &.minorDelay,
        &.moderateDelay {
          color: $snw-delayed-color;
        }
        &.severeDelay,
        &.cancelled {
          color: $snw-cancelled-color;
        }
      }
    }
    .snw-service-step-icon {
      padding-top: $snw-main-padding;
      padding-bottom: $snw-main-padding;
      .snw-service-icon {
        width: $snw-icon-size;
        height: $snw-icon-size;
        display: inline-block;
        background-repeat: no-repeat;
        background-size: contain;
        &.snw-icon-train {
          background-image: url('../images/icons2x/snw-rdg-icon-railway-red.png');
        }
        &.snw-icon-bus,
        &.snw-icon-scheduled_bus,
        &.snw-icon-replacement_bus {
          background-image: url('../images/icons2x/bus.png');
        }
        &.snw-icon-walk {
          background-image: url('../images/icons2x/walk.png');
        }
        &.snw-icon-tramlink {
          background-image: url('../images/icons2x/tramlink.png');
        }
        &.snw-icon-underground {
          background-image: url('../images/icons2x/underground.png');
        }
        &.snw-icon-ferry {
          background-image: url('../images/ferry.png');
        }
        &.snw-icon-transfer {
          background-image: url('../images/icons2x/transit-transfer.png');
        }
        &.snw-icon-overground {
          background-image: url('../images/snw-step-overground.svg');
        }
      }
    }
    .snw-service-step-line {
      width: 4px;
      background: black;
      min-height: $snw-service-step-height;
      .snw-service-step-line-circle {
        border: 2px solid black;
        width: $snw-service-line-circle-size;
        height: $snw-service-line-circle-size;
        background: white;
        border-radius: $snw-service-line-circle-size/2;
        position: relative;
        top: 0;
        left: -6px;
        &.circle-last {
          &:before {
            content: '';
            display: block;
            width: 6px;
            height: 6px;
            background: #000;
            border-radius: 50%;
            position: absolute;
            top: 3px;
            left: 3px;
          }
        }
      }
      img {
        height: $snw-service-line-circle-size;
        width: $snw-service-line-circle-size;
        margin-left: -5px;
        margin-top: -5px;
      }
    }
    .snw-service-step-station {
      width: calc(100% - 100px);
      padding-left: 25px;
      margin-top: -$snw-service-step-circle-top;
      padding-bottom: 20px;
      &.snw-service-calling-point {
        min-height: $snw-service-step-height;
        .snw-service-station-name {
          font-size: smaller;
        }
      }
      .snw-service-step-tram-stops {
        font-size: smaller;
        color: #676766;
      }
    }
    .snw-service-step-mode {
      padding-top: $snw-main-padding;
      .snw-service-mode.snw-service-step-duration {
        font-size: .9em;
        margin-top: 1px;
      }
      .snw-service-name {
        text-transform: capitalize;
      }
      .snw-service-company {
        padding: 5px;
        background-color: $snw-company-background;
        color: $snw-company-color;
        line-height: 30px;
      }
      .snw-service-mode {
        padding: 5px;
        color: $mid-grey;
        margin-top: 10px;
      }
    }
    .snw-service-step-disruption {
      padding-top: $snw-main-padding;
      .snw-disruption-icon {
        vertical-align: bottom;
      }
      .snw-disruption-check {
        display: block;
        font-size: smaller;
        cursor: pointer;
        text-decoration: underline;
        padding-left: 24px;
      }
      .snw-disruption-type {
        padding: 5px;
        position: relative;
        top: -5px;
      }
      .snw-disruption-msg {
        display: none;
        font-size: smaller;
        text-align: justify;
        padding-left: 24px;
        i {
          display: block;
          cursor: pointer;
        }
      }
    }
    &:first-child .snw-service-step-line {
      // So the line don't start at the top of the div
      .snw-service-step-line-circle {
        margin-top: 0;
      }
    }
    &.snw-service-step-selected {
      font-weight: bold;
    }
  }
  .snw-service-train-icon {
    position: absolute;
    margin-top: $snw-train-icon--margin-top;
    top: 0px;
    left: 97px;
    z-index: 1000;
    width: $snw-train-icon--size;
    height: $snw-train-icon--size;
    background-image: url('../images/icons1x/icon-train-big.png');
    background-size: $snw-train-icon--size;
    &.hidden {
      display: none;
    }
  }
}