.snw-layer-categorized-container {
  transition: height 300ms ease-in-out;
}

.snw-layers-list {
  .snw-collapse-btn {
    cursor: pointer;
  }
  .snw-icon {
    width: 35px;
    height: 30px;
    color: $snw-list-icon-color;
    margin-right: 10px;
  }
  .snw-name {
    width: 100%;
  }
  .snw-toggle {
    width: 50px;
  }
}