.snw-single-travel-new {
  padding: $snw-main-padding;
  padding-right: 30px;
  position: relative;
  &:before {
    display: block;
    position: absolute;
    bottom: 10px;
    right: 10px;
    content: '>>';
    color: #272460;
    font-size: .8em;
  }
  .snw-travel-new-summary {
    font-size: $snw-header-size;
    padding-bottom: $snw-main-padding;
    .snw-time {
      width: 100%;
      text-align: left;
      font-size: 1em;
      color: #999999;
    }
  }
  .snw-travel-new-content {
    width: 100%;
    .snw-travel-new-description {
      text-align: justify;
      font-size: 14px;
      overflow: hidden;
    }
  }
}