 .snw-route-details-container {
   -webkit-box-shadow: $snw-box-shadow;
   -moz-box-shadow: $snw-box-shadow;
   box-shadow: $snw-box-shadow;
   font-size: $snw-font-size;
   color: $snw-font-color;
   margin: 0;
   bottom: 0;
   position: relative;
   background-color: $snw-component-background;
   height: 100%;
   z-index: 100;
   .snw-service-header {
     height: $snw-header-component-height;
     background-color: $snw-component-background;
     color: $snw-font-color;
     -webkit-box-shadow: $snw-box-shadow;
     -moz-box-shadow: $snw-box-shadow;
     box-shadow: $snw-box-shadow;
     .snw-service-title {
       margin-left: 35px;
       font-size: $snw-tabs-font-size;
       padding: 7px;
       width: calc(100% - #{$snw-icon-size} - 55px);
       @include truncate();
       .snw-header-label {
         color: $snw-secondary-color;
       }
       .snw-service-from,
       .snw-service-to {
         @include truncate();
       }
     }
     .snw-icon-close,
     .snw-icon-back {
       z-index: 11;
       position: absolute;
       right: 0;
       top: 0;
       padding: $snw-main-padding;
       width: 90%;
       cursor: pointer;
       &:before {
         width: $snw-icon-size;
         height: $snw-icon-size;
       }
     }
     .snw-icon-back {
       left: 0;
       color: $snw-title-font-color;
     }
     .snw-favourite-route {
       position: absolute;
       top: 0;
       right: 0;
       display: block;
       cursor: pointer;
       padding: 10px;
       font-size: 25px;
       &.isFavourite {
         &:before {
           content: '\e838';
         }
       }
     }
   }
   .snw-disclaimer-message {
     display: none;
     position: relative;
     background-color: $snw-disclaimer-background;
     color: black;
     padding: 15px 30px 15px 50px;
     background-image: url('/images/icons2x/snw-icon-alert-message.png');
     background-size: auto 25px;
     background-repeat: no-repeat;
     background-position: 15px center;
     &[style="display: block;"]+.snw-directions-box-routes-panel {
       max-height: calc(100vh - 310px);
     }
     span {
       font-size: 0.8em;
       vertical-align: middle;
     }
     .close {
       &:before {
         content: "\e93f";
       }
       position: absolute;
       top: 5px;
       right: 5px;
       font-size: 15px;
       color: #91908F;
       cursor: pointer;
     }
   }
   .snw-service-content {
     max-height: calc(100vh - 170px);
     display: flex;
     flex-direction: column;
     cursor: default;
     .snw-alternative-routes {
       margin: 0;
       display: inline-block;
       width: 110px;
       height: 37px;
       background-repeat: no-repeat;
       background-color: $snw-cancelled-color;
       background-image: url(../images/icons1x/swn-rdg-icon-check-journey-white-22-x-26.png);
       background-position: 6px;
       padding: 3px 0px 0px 35px;
       font-size: .9em;
       color: #fff;
       cursor: pointer;
       background-size: 20px;
       text-align: left;
       span {
         cursor: pointer !important;
       }
     }
   }
   .snw-service-content {
     span {
       cursor: default;
     }
     .snw-service-time-info {
       .snw-service-date {
         display: inline-block;
       }
     }
   }
   .snw-cta-see-route {
     display: none;
   }
 }

 @media (max-width:$snw-break-mobile) {
   .snw-service-container {
     width: 100%;
   }
   .snw-service-container-collapse {
     top: 0 !important;
     .snw-disclaimer {
       display: none;
     }
     .snw-cta-see-route {
       display: none !important;
     }
   }
   .snw-service-delayed {
     .snw-cta-see-route {
       display: inherit;
     }
   }
   .snw-route-details-container {
     .snw-service-header {
       .snw-favourite-route {
         padding: 5px;
         font-size: 30px;
       }
     }
     .snw-disclaimer-message[style="display: block;"]+.snw-service-content {
       height: calc(100% - 100px) !important;
     }
     .snw-service-content {
       overflow-y: auto;
       max-height: calc(100% - 45px);
       .snw-alternative-routes {
         width: 145px;
         .snw-alternative-routes-text {
           display: block;
           margin-top: 7px;
         }
       }
       .snw-service-steps {
         background-color: $snw-component-background;
         max-height: initial !important;
         height: initial;
         overflow-y: initial;
         margin-bottom: 50px;
         .snw-service-step {
           .snw-service-company {
             line-height: 30px;
           }
         }
       }
     }
     .snw-cta-see-route {
       position: absolute;
       bottom: 0;
       display: inline-block;
       height: 35px;
       width: 100%;
       text-align: center;
       padding: 8px;
       font-size: 13px;
       box-shadow: 0px -4px 7px -2px rgba(0, 0, 0, 0.25);
     }
   }
 }