.snw-toggle {
  position: relative;
  cursor: pointer;
  height: 25px;
  cursor: pointer;
  .snw-onoffswitch-toggle {
    position: relative;
    margin-left: -9999px;
    visibility: hidden;
  }
  .snw-onoffswitch-label {
    display: block;
    position: absolute;
    z-index: 1;
    pointer-events: none;
    transition: background 0.4s;
    text-indent: -9999px;
    top: 5px;
    left: $snw-toggle-ball-left;
    width: $snw-toggle-ball-size;
    height: $snw-toggle-ball-size;
    background-color: $snw-toggle-ball-bg-color;
    border-radius: 50%;
    border: none;
    @include box-shadow(0, 0, 10px, 2px, rgba(153, 153, 153, .57));
    transition: left 0.4s, background 0.4s;
  }
  .snw-onoffswitch-toggle~ .snw-toggle-bar {
    width: $snw-toggle-bar-width;
    height: $snw-toggle-bar-height;
    position: absolute;
    top: $snw-toggle-bar-top;
    background-color: $snw-toggle-bar-bg-color;
    border-radius: 60px;
    transition: background 0.4s;
    z-index: 0;
  }
  .snw-onoffswitch-toggle:checked~ .snw-toggle-bar {
    background-color: $snw-toggle-bar-bg-color--active;
  }
  .snw-onoffswitch-toggle:checked+ .snw-onoffswitch-label {
    left: $snw-toggle-bar-left--active;
    background-color: $snw-toggle-ball-bg-color--active;
    border: none;
  }
}
