.snw-favourite-stations {
  .snw-station {
    padding: $snw-main-padding 5px;
    display: flex;
    &.snw-selectable {
      cursor: pointer;
    }
  }
  .snw-icon-station {
    background-image: url(../images/icons1x/snw-railway-icon.png);
    width: 20px;
    background-size: contain;
  }
  .snw-station-content {
    padding-left: $snw-main-padding;
  }
}