.snw-station-info-board {
  margin: 0!important;
  box-shadow: none;
  .station-message-header {
    .header-text {
      cursor: default;
      text-align: center;
      color: #777;
      padding: 10px;
      top: 10px;
      position: relative;
      span {
        position: relative;
        top: 1px;
        padding-right: 9px;
        &.icon {
          font-family: 'snw-material-icons';
          &:before {
            font-size: 15px;
          }
          &.icon-station-alert:before {
            content: "\e002";
            color: $snw-station-blue;
          }
        }
      }
    }
  }
  .stationAlert {
    padding-bottom: 1em;
    border-bottom: 1px solid #eee;
    p,
    div {
      margin: 1em;
      color: #666;
    }
    strong,
    p,
    span,
    li,
    div {
      font-size: 0.8em!important;
    }
    p>strong,
    p>strong>span,
    p>span,
    li>span {
      font-size: 1em!important;
    }
  }
  .section-header {
    color: white;
    background-color: #84D2F5;
    line-height: 2.5em;
    font-size: 0.8em;
    padding-left: 10px;
    outline: none;
    cursor: pointer;
    border-bottom: 3px solid #fff;
    margin-bottom: 0;
  }
  .ui-accordion-header-icon {
    float: right;
    margin: 7px;
  }
  .snw-md-icon-check {
    padding-left: 9px;
    color: green;
  }
  .snw-md-icon-error {
    padding-left: 9px;
    color: red;
  }
  .toilets-available {
    display: inline-block;
    vertical-align: middle;
  }
  .station-facilities {
    cursor: default;
    text-align: center;
    color: #666;
    padding: 10px;
    box-shadow: 0px 0px 6px -1px #666;
    position: relative;
    z-index: 999;
  }
  h3 {
    margin-top: 0;
  }
  .section {
    cursor: default;
    font-size: 0.8em;
    line-height: 1.5em;
    padding: 10px;
    padding-bottom: 0px;
    .subsection {
      padding-bottom: 10px;
      .data-title {
        font-weight: bold;
      }
      .data-content {
        padding-top: 5px;
        table {
          width: 100%;
          th,
          td {
            width: 55%;
          }
        }
        .price {
          padding-left: 40px;
        }
        a,
        p,
        span {
/*           a {
            font: initial !important;
          } */
          &:not(a) {
            color: $snw-font-color !important;
          }
          background-color: white !important;
          font-size: 1em !important;
        }
      }
    }
    .snw-charge,
    .snw-card-park-info {
      .data-content {
        padding-left: 15px;
      }
    }
  }
}