$snw-journey-tabs-height-mobile: 48px;
$snw-journey-tabs-height: 70px;

.snw-journey-container {
  -webkit-box-shadow: $snw-box-shadow;
  -moz-box-shadow: $snw-box-shadow;
  box-shadow: $snw-box-shadow;
  font-size: $snw-font-size;
  color: $snw-font-color;
  margin: 15px;
  bottom: 15px;
  position: absolute;
  background-color: $snw-component-background;
  max-height: calc(100% - 30px);
  height: auto;
  width: $snw-component-width;
  z-index: 1000010!important;
  height: 0;

  .snw-journey-header {
    height: $snw-journey-tabs-height;
  }

  .snw-journey-content {
    width: 100%;
    position: absolute;
  }

  .snw-cta-see-map {
    display: none;
    cursor: pointer;
  }
}
@media (max-width:$snw-break-mobile) {
  .snw-journey-container {
    width: 100%;
    margin: 0;
    bottom: 0;
    height: 0;
    min-height: 48px;
    max-height: 100%;

    &.snw-directions-box-collapse-see-map {
      height: 0!important;
    }

    &.snw-directions-box-active {
      height: 100%;
    }

    &.snw-travel-news-active {
      height: 100% !important;
    }

    &.snw-collapsed {
      height: 0;
    }

    .snw-journey-content {
      height: calc(100% - #{$snw-journey-tabs-height-mobile});
      max-height: calc(100% - #{$snw-journey-tabs-height-mobile});
      .snw-container-fix {
        height: $snw-journey-tabs-height-mobile;
        background: transparent;
        border: 0;
        box-shadow: none;
      }
    }

    .snw-cta-see-map {
      position: absolute;
      bottom: 0;
      height: 35px;
      width: 100%;
      text-align: center;
      padding: 8px;
      font-size: 13px;
      box-shadow: 0px -4px 7px -2px rgba(0,0,0,0.25);
    }

    .snw-journey-header {
      height: $snw-journey-tabs-height-mobile;
    }

    &.snw-journey-container-collapse {
      height: 0;
      position: absolute;

      .snw-journey-content {
        overflow-y: initial;
      }

      &.snw-container-fix {
        .snw-service-container-collapse {
          display: none!important;
        }
      }
    }
  }
}
