.snw-custom-panel {
  position: relative;
  border-top: 1px solid $component-border;
  .snw-header {
    background-color: $snw-secondary-background;
    // display: none;
    visibility: hidden;
    width: 100%;
    height: 35px;
    padding: $snw-main-padding;
    color: $mid-grey;
    .snw-title-favourite-stations,
    .snw-title-nearby-stations,
    .snw-title-favourite,
    .snw-title-following {
      position: relative;
      bottom: 8px;
    }
    .snw-icon-company-stations,
    .snw-icon-favourite-stations,
    .snw-icon-nearby-stations,
    .snw-md-icon-star-full,
    .snw-icon-following {
      color: $snw-secondary-color;
      padding-right: 5px;
      position: relative;
      bottom: 3px;
    }
  }
  .snw-content {
    padding: $snw-main-padding;
    color: $mid-grey;
    .snw-item:hover {
      background-color: $snw-secondary-background;
    }
  }
  #snw-custom-panel-accordion {
    .ui-accordion-header-icon {
      float: right;
    }
    .snw-accordion-section-header {
      line-height: 2.5em;
      font-size: 1em;
      padding-left: 10px;
      outline: none;
      cursor: pointer;
    }
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-custom-panel {
    .snw-header {
      .snw-title-favourite-stations,
      .snw-title-following,
      .snw-title-nearby-stations {
        bottom: 10px;
      }
      .snw-icon-company-stations,
      .snw-icon-favourite-stations,
      .snw-icon-nearby-stations,
      .snw-icon-following {
        bottom: 7px;
      }
    }
  }
}