.snw-incident-details-panel {
  z-index: 10000000 !important;
  margin: 0;
  height: 100%;
  width: $snw-component-width;
  cursor: default;

  .snw-incident-details-header {
    height: $snw-header-component-height;
    background-color: $snw-header-component-background;
    color: $snw-title-font-color;
    .snw-incident-details-title {
      font-size: $snw-title-font-size;
      padding: $snw-main-padding;
      width: calc(100% - #{$snw-icon-size});
      text-align: center;
      @include truncate();
      &.snw-incident-details-title--has-back {
        width: calc(100% - #{$snw-icon-size} - 50px);
        margin-left: 50px;
      }
    }
    .snw-icon-close {
      z-index: 11;
      position: absolute;
      right: 0;
      top: 0;
      padding: $snw-main-padding;
      cursor: pointer;
      width: 40px;
      &:before {
        width: $snw-icon-size;
        height: $snw-icon-size;
      }
    }
    .snw-icon-back {
      left: 0;
      position: absolute;
      color: #fff;
      padding: 10px;
      cursor: pointer;
    }
  }
  .snw-incident-details-content {
    overflow-y: auto;
    height: calc(100% - #{$snw-header-component-height});
    -webkit-text-size-adjust: none;
    .snw-incident-details-summary {
      font-size: $snw-title-font-size;
      padding: $snw-main-padding;
    }
    .snw-incident-details-description {
      font-size: $snw-tabs-font-size;
      padding: $snw-main-padding;
    }
    .snw-incident-details-info {
      padding: $snw-main-padding;
    }
    .snw-incident-tocs {
      border-top: 1px solid #f5f5f5;
      border-bottom: 1px solid #efefef;
      padding: 10px;
      h3 {
        margin-top: 6px;
        font-size: 16px;
      }
      p {
        font-size: 14px;
      }
    }
    .snw-incident-tweets {
      // overflow: auto;
      & > h3 {
        text-align: center;
        border-bottom: 1px solid #f0f0f0;
        padding: 10px 0;
        margin: 0;
      }
      .snw-noResults,
      .snw-serviceError {
        display: none;
        color: $snw-secondary-color;
        text-align: center;
        top: 20%;
        position: relative;
        margin: 2em 0 4em;
        .snw-icon-info {
          &:before {
            font-family: 'snw-material-icons';
            content: '\e88f';
            font-size: 77px;
          }
        }
        .snw-serviceError-text {
          font-size: 17px;
        }
      }
    }
  }
}

@media (max-width:$snw-break-mobile) {
  .snw-incident-details-panel {
    width: 100%;   
    .snw-incident-details-content {
      padding-bottom: 30px;
    }
    .snw-cta-see-map {
      position: absolute;
      bottom: 0;
      display: inline-block;
      height: 35px;
      width: 100%;
      font-size: 1.5em;
      text-align: center;
      padding: 8px;
      font-size: 13px;
      box-shadow: 0px -4px 7px -2px rgba(0,0,0,0.25);
    }
  }
}