.snw-favourite-routes {
    .snw-favourite-route {
        cursor: pointer;
        padding: 10px 5px;
        display: flex;
        .snw-icon-route {
            background-image: url(../images/icons1x/swn-rdg-icon-check-journey-grey-22-x-26.png);
            width: 30px;
            height: 30px;
            background-size: contain;
            margin-right: 5px;
        }
        .snw-route-content {
            width: calc(100% - 30px);
            padding-top: 7px;
            display: flex;
            span.name {
              display: inline-block;
                margin-right: 4px;
                text-overflow: ellipsis;
                overflow: hidden;
                vertical-align: top;
            }
            span.route-code {
                display: inline-block;
                position: relative;
                font-style: normal;
            }
            span.snw-route-content-origin.content-element,  span.snw-route-content-destination.content-element {
                display: flex;
            }
            .snw-route-content-origin,
            .snw-route-content-destination {
                @include truncate();
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
                display: inline-block;
            }
            .snw-route-content-separator {
              margin: 0 5px;
            }
        }
    }
}
