.panelMenu {
  @include category-btn(#fff);
  cursor: pointer;
  i {
    @include transition(transform 0.3s);
  }
  &.selected {
    background-color: #F8F8F8;
    i {
      @include transition(transform 0.3s);
      @include rotate(180);
    }
  }
  width: 50px;
  margin: 0;
}

.panel-menu {
  margin: 0;
  .panel-menu-toolbox {
    @include center-element('space-between');
    margin: 10px;
    border: 1px solid #b3b3b3;
  }
}

&[max-width='720px'] {
  .main-menu {
    .main-menu-fluid-panel {
      width: calc(100% - 20px);
    }
  }
}

.panelMenu {
  width: 70px;
  .icon-container {
    height: 26px;
    top: calc(50% - 13px);
    position: relative;
    border-left: 1px solid #d2d2d2;
    color: green;
    .tabPanel-icon {
      height: 20px;
      width: 20px;
      cursor: pointer;
    }
  }
  &.selected {
    .icon-container {
      border: none;
      .tabPanel-icon {
        background-image: none;
        &:before {
          content: "\e945";
        }
        &:hover {
          background-image: none;
        }
      }
    }
  }
}
