.snw-main-menu {
  width: $snw-main-menu-width;
  background-color: $snw-main-menu-bg-color;
  padding: $snw-main-menu-padding;
  @include component-box-shadow();
  .snw-main-menu-header,
  .snw-main-menu-toolbox {
    @include rounded($snw-radius);
    position: relative;
    @include center-element(space-around);
  }
  .snw-main-menu-header {
    height: $snw-main-menu-height-header;
  }
  .main-menu-container {
    @include component-box-shadow();
    overflow-y: auto;
    overflow-x: hidden;
    z-index: 1;
    max-height: $snw-main-menu-maxheight;
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-main-menu {
    overflow: auto;
    width: $snw-main-menu-width--mobile;
    max-height: $snw-main-menu-maxheight--mobile;
  }
}
