.snw-list {
  padding: 0;
  margin: 0;
  cursor: default;
  .snw-list-item {
    background-color: $snw-list-bg-color;
    font-size: $snw-list-font-size;
    padding: $snw-list-padding;
    color: $snw-list-color;
    border-bottom: $snw-list-border;
    @include center-element();
    &.snw-active {
      color: $snw-list-icon-color--active;
      .snw-icon {
        color: $snw-list-color--active;
      }
    }
    &:hover {
      background-color: $snw-list-bg-color--hover;
    }
  }
}

.snw-inline-list {
  @include center-element();
  .snw-inline-item {
    @include center-element();
  }
}