@import '../../../../node_modules/jquery-unslider/dist/sass/unslider.scss';
@import '../../../../node_modules/jquery-unslider/dist/sass/unslider-dots.scss';
.snw-carousel {
  position: relative;
  .snw-carousel-container,
  .unslider {
    text-align: center;
    height: 100%;
    overflow: hidden;
    li {
      position: relative;
      img {
        width: 100%;
        height: 100%;
      }
    }
    &.snw-no-data {
      position: relative;
      height: auto;
      top: 30%;
      color: #5d5d5d;
      padding: 10px;
    }
    .unslider-arrow {
      top: calc(50% - 15px);
      text-indent: -9999px;
      right: 20px;
      width: 20px;
      height: 25px;
      background-repeat: no-repeat;
      background-position: center;
      opacity: 1;
      &:hover {
        opacity: 0.6;
      }
      &.next {
        right: 0;
        background-image: url("../images/icn_right_fff_shadow.png");
      }
      &.prev {
        left: 0;
        background-image: url("../images/icn_left_fff_shadow.png");
      }
    }
    .unslider-nav {
      position: absolute;
      bottom: 2px;
      width: 100%;
      height: 20px;
      background: linear-gradient(to top, rgba(0, 0, 0, 0.27), rgba(255, 255, 255, 0));
      li {
        border: 1px solid #fff;
        background-color: #fff;
        opacity: 0.6;
        &.unslider-active {
          opacity: 1;
        }
      }
    }
    &.snw-numbers {
      .unslider-nav {
        ol li {
          display: none;
          background-color: none;
          border: none;
          &.unslider-active {
            border: none;
            background-color: transparent;
            display: inline;
            text-shadow: 0 1px 2px rgba(0, 0, 0, 0.7);
            color: #fff;
            font-size: 14px;
          }
        }
      }
      .unslider-arrow {
        top: calc(100% - 25px);
        opacity: 1;
        background-color: transparent;
        &:hover {
          opacity: 0.6;
        }
        &.next {
          right: 100px;
        }
        &.prev {
          left: 100px;
        }
      }
    }
  }
}