.snw-panel-btn {
  @include category-btn(#fff);
  width: 70px;
  cursor: pointer;
  margin: 0;
  box-sizing: border-box;
  .snw-icon-container {
    height: 26px;
    width: 45px;
    top: calc(50% - 13px);
    position: relative;
    border-left: 1px solid #d2d2d2;
    color: green;
    @include center-element();
  }
  &.selected {
    background-color: #F8F8F8;
    .snw-icon-container {
      width: 46px;
      border: none;
    }
  }
}

.snw-panel-btn.selected+ div {
  div {
    border-left: none!important;
  }
}