.snw-routes-panel {
  height: 100%;

  &[max-width='720px'] {
    height: 0;
  }

  .prediction-message {
    padding: 1em 1em 1em 3em;
    background: #FCF8E2;
    border-left: 5px solid #E28822;
    position: relative;

    & > i.snw-icon-info {
      color: #E28822;
      position: absolute;
      left: 0.6em;
    }

    & > i.snw-icon-close {
      color: #E28822;
      position: absolute;
      top: 3px;
      right: 3px;
      font-size: 14px;
      cursor: pointer;
    }

    & > p {
      margin: 0;
    }
  }

  .snw-routes-list {
    pointer-events: all;
    list-style-type: none;
    display: inline-block;
    padding: 0;
    margin: 0;

    &.hasRoutes {
      width: 100%;
    }

    li.snw-route {
      background-color: $snw-component-background;
      cursor: pointer;

      .snw-service-delayed-msg {
        display: none;
      }

      .snw-service-delayed {
        .snw-cell-right {
          text-align: right;
        }
        display: table;
      }

      .snw-disruption-msg {
        display: none;
      }

      &:not(:last-child) {
        border-bottom: 1px solid $component-border;
      }

      .snw-details {
        display: inline-block;
      }

      .snw-alternative-routes {
        display: none;
      }

      .snw-disclaimer {
        display: none;
      }

      &:first-child {
        border-top: 1px solid $component-border;

        .snw-disclaimer {
          display: block;
          padding: 10px 25px;
          color: #DE8D23;
          text-align: justify;
          font-size: 13px;
        }

        .snw-details {
          display: inline-block;
        }
      }

      &.selected {
        background-color: $snw-secondary-background;
      }

      &.highlighted {
        background-color: $snw-secondary-hover-background;
      }

      .snw-service-station-info {
        .snw-service-station-row {
          .swn-service-station-info-btn-see-map {
            display: none;
          }
        }
      }
    }
  }
}

&[max-width='720px'] {
  .snw-routes-panel {
    .snw-routes-list {
      &.hasRoutes {
        height: 100%;
      }

      .snw-service-delayed-cell {
        .snw-cta-see-map {
          display: none;
        }
      }

      li.snw-route {
        &:first-child {
          .snw-cta-see-map-list-routes {
            display: block;
          }
        }
      }
    }
  }
}
