.snw-header {
  .snw-icon-nearby-stations {
    &:before {
      content: '\e569';
    }
  }
}

.snw-nearby-stations {
  .snw-station {
    padding: $snw-main-padding 5px;
    display: flex;
    &.snw-selectable {
      cursor: pointer;
    }
  }
  .snw-icon-station {
    background-image: url(../images/icons1x/snw-railway-icon.png);
    width: 20px;
    background-size: contain;
  }
  .snw-station-content {
    display: flex;
    width: 60%;
    padding-left: $snw-main-padding;
    .snw-station-name {
      @include truncate();
    }
    .snw-station-crs {
      padding-left: 5px;
    }
  }
  .minorDisruption {
    width: 10%;
    font-family: 'snw-material-icons' !important;
    color: #00466E;
    text-align: center;
    &:before{
      content: '\e002';
    }
  }
  .snw-walk-content {
    position: absolute;
    width: 25%;
    right: 0;
    .snw-time-content {
      position: absolute;
      display: inline;
      margin: 2px 5px 10px 5px;
    }
  }
}

&[max-width='720px'] {
  .snw-nearby-stations {
    .snw-station-content {
      width: 50%;
    }

    .minorDisruption {
      width: 15%;
    }

    .snw-walk-content {
      width: 30%;
    }
  }
}