.snw-icon-warning {
  cursor: pointer;
  display: inline;
  &:before {
    color: $snw-cancelled-color;
    font-size: 25px;
    background-image: url('../images/white_circle.svg');
    background-size: 60%;
    background-repeat: no-repeat;
    background-position: center;
  }
  &.snw-marker-selected {
    &:before {
      font-size: 30px;
    }
    top: 3px;
    position: relative;
  }
}

.snw-visible-station {
  margin-left: -25px;
  margin-top: 14px;
  &.snw-multiple-incidents-2 {
    margin-left: -40px;
  }
  &.snw-multiple-incidents-3 {
    margin-left: -60px;
  }
  &.snw-multiple-incidents-4 {
    margin-left: -80px;
  }
}

.snw-visible-service-station {
  margin-left: -25px;
  margin-top: 14px;
  &.snw-multiple-incidents-2 {
    margin-left: -40px;
  }
  &.snw-multiple-incidents-3 {
    margin-left: -60px;
  }
  &.snw-multiple-incidents-4 {
    margin-left: -80px;
  }
}