.snw-selected-time {
  color: #878787;
}

.snw-md-icon-arrow-down {
  font-family: inherit !important;
  font-size: 14px;
  text-align: left;

  &:hover {
    color: $snw-icon-color;
  }

  &:after {
    content: '\e5c5';
    font-family: 'snw-material-icons'!important;
    position: relative;
    top: 2px;
  }
}

.snw-md-icon-clock {
  position: relative;
  width: 25px;
  bottom: -5px;
  content: url('../images/icons2x/snw-clock-icon.png');
  text-align: left;
}

.snw-md-icon-calendar {
  position: relative;
  width: 25px;
  bottom: -5px;
  content: url('../images/icons2x/snw-calendar-icon.png');
  text-align: left;
}

.snw-parameters-timeselector {
  position: absolute;
  top: 27px;
  left: 0px;
  list-style: none;
  background-color: $snw-hover-color;
  padding: 0px;
  display: none;
  z-index: 2;
  box-shadow: 2px 2px 5px -3px black;
  @include box-shadow();

  li {
    color: $brand-blue;
    padding: 15px;
    cursor: pointer;
    font-size: 14px;

    &:hover {
      background-color: $snw-secondary-background;
    }
  }
}

#datePickerElement {
  height: 80px;

  span {
    display: inline-block;
  }

  .inputsPickers {
    display: flex;
    justify-content: space-around;
    padding-right: 11px;

    .divDatePicker,
    .divTimePicker {
      width: 40%;
      position: relative;

      input {
        width: 80%;
        border: 0;
        outline: 0;
        background-color: $snw-secondary-background;
        border-bottom: 1px solid #cacaca;
        cursor: pointer;
        color: #727272;
        font-size: 1.2em;
      }
    }
  }

  .headerPanelTimeMode {
    display: flex;
    justify-content: space-between;
    padding: 0 4px;
    margin-bottom: 10px;

    .snw-selected-time {
      cursor: default;
      margin-left: 15px;
    }

    i {
      color: #91908F;
      width: 20px;
      font-size: 1.5em;
      margin-left: 0.5em;

      &.reset {
        font-size: 1.5em;
        cursor: pointer;
        padding-left: 8px;
        margin-left: 0.5em;
        color: $brand-blue;
      }
    }
  }
}

&[max-width='720px'] {
  #datePickerElement {
    .inputsPickers {
      input {
        width: 70%;
        font-size: 0.9em;
      }
    }
  }
}
