.snw-lateral-menu {
  margin: 0;
  * {
    font-family: $snw-font-family;
    box-sizing: border-box;
  }
  .snw-lateral-panel {
    overflow-x: hidden;
    overflow-y: auto;
    position: absolute;
    top: 0;
    z-index: 1;
    width: 330px;
    height: calc(100% - 30px);
    background-color: #fff;
    opacity: 0;
    transform: translateX(0);
    opacity: 0;
    transform: translateX(-110%);
    transition: .4s cubic-bezier(.21, .65, .37, .91);
    .snw-lateral-menu-header {
      background-color: $snw-lateral-panel-header-bg-color;
      .snw-toggle-btn {
        margin-left: auto;
        .snw-icon {
          font-size: $snw-lateral-menu-icon-size;
          color: $snw-gray;
        }
      }
    }
    &.snw-visible-panel {
      opacity: 1;
      transform: translateX(0);
      transition: transform .4s cubic-bezier(.21, .65, .37, .91), opacity .2s cubic-bezier(.21, .65, .37, .91);
    }
  }
}

.snw-lateral-panel+ .snw-overlay {
  background-color: black;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  opacity: 0.4;
  visibility: hidden;
}

.snw-lateral-panel.snw-visible-panel+ .snw-overlay {
  visibility: visible;
}

&[max-width='720px'] {
  .snw-lateral-menu {
    .snw-lateral-panel {
      width: calc(100% - 20px);
    }
  }
}