.snw-calculate-travel-time {
  .snw-icon {
    color: #bbbbbb;
    font-size: 24px;
    cursor: pointer;
  }
  .snw-panel-header {
    border-bottom: 0;
  }
  .snw-travel-time-box {
    .snw-list-item {
      border: solid 1px #e6e6e6;
      min-height: 35px;
      height: 35px;
      justify-content: space-between;
      position: relative;
      .snw-icon-info {
        height: 25px;
        width: 35px;
        padding-right: 2px;
        border-right: solid 1px #e6e6e6;
        margin: 2px;
        color: #0080BD;
      }
      .snw-input-autocomplete {
        border: none;
        width: calc(100% - 35px);
        padding-left: 10px;
        font-size: 11px;
      }
    }
    .snw-popup {
      .snw-message {
        background-color: #0080BD;
        color: white;
        top: -8px;
        &:before {
          border-right: 7px solid #0080BD;
        }
      }
    }
  }
  .snw-point-interest {
    margin-bottom: 10px;
    color: #4d4d4d;
    padding-left: 25px;
    background-repeat: no-repeat;
    background-position: 0px center;
    background-size: 12px 20px;
    .snw-address-point-interest {
      font-size: 13px;
      width: calc(100% - 30px);
      .snw-street-point-interest {
        font-weight: 600;
      }
      .snw-street-point-interest,
      .snw-city-point-interest {
        width: 100%;
        @include truncate();
        margin-bottom: 5px;
      }
    }
  }
  .snw-transports-tt {
    margin: 30px 0;
  }
}

.snw-marker-label-hidden {
  display: none;
}

.snw-marker-poi {
  width: 15px;
  height: 30px;
}

//ICONS MARKERS AND LIST
@for $i from 1 through 6 {
  .snw-marker-poi.snw-poi-#{$i} {
    background-image: url('../images/my_poi_default_#{$i}.png');
  }
  .snw-point-interest.snw-poi-#{$i} {
    background-image: url('../images/my_poi_preview_#{$i}.png');
  }
  .snw-marker-poi.snw-active.snw-poi-#{$i} {
    background-image: url('../images/my_poi_active_#{$i}.png');
  }
}
