.snw-message-control {
  opacity: 0.8;
  z-index: 101!important;
  .snw-message {
    display: block;
    .snw-icon {
      vertical-align: middle;
      display: table-cell;
    }
    .snw-message-text {
      line-height: 20px;
      display: table-cell;
      word-wrap: break-word;
      width: calc(100% - 60px);
      padding-left: $snw-main-padding;
      padding-right: 30px;
      a {
        white-space: nowrap;
      }
    }
    .snw-icon-close {
      position: absolute;
      top: 0;
      right: 0;
      margin: $snw-secondary-margin;
    }
  }
  &.moderateDelay {
    .snw-icon-warning {
      color: $snw-delayed-color;
    }
  }
  &.severeDelay {
    .snw-icon-warning {
      color: $snw-cancelled-color;
    }
  }
  &.ontime {
    .snw-icon-warning {
      color: $snw-ontime-color;
    }
  }
}

@media (max-width: $snw-break-mobile) {
  .snw-message-control.snw-component-on-map {
    width: 100%;
    margin: 0;
    bottom: 0;
    position: absolute;
  }
}
