.snw-travel-news-panel {
  position: static!important;
  overflow-y: auto;
  max-height: 100%; // Fixes DE203
  //max-height: calc(100vh - 120px); //Fixes DE247  
  max-height: calc(100vh - 78px);
  background: white;
  .snw-disclaimer-message {
    display: none;
    position: relative;
    background-color: $snw-disclaimer-background;
    color: black;
    padding: 15px 30px 15px 50px;
    background-image: url('/images/icons2x/snw-icon-alert-message.png');
    background-size: auto 25px;
    background-repeat: no-repeat;
    background-position: 15px center;
    &[style="display: block;"]+.snw-directions-box-routes-panel {
      max-height: calc(100vh - 310px);
    }
    span {
      font-size: 0.8em;
      vertical-align: middle;
    }
    .close {
      &:before {
        content: "\e93f";
      }
      position: absolute;
      top: 5px;
      right: 5px;
      font-size: 15px;
      color: #91908F;
      cursor: pointer;
    }
  }
  .snw-travel-news-list {
    pointer-events: all;
    list-style-type: none;
    display: inline-block;
    padding: 0;
    margin: 0;
    overflow: hidden;
    max-width: 100%;
    list-style-position: inside;
    width: 100%;
    li.snw-travel-new {
      background-color: $snw-component-background;
      cursor: pointer;
      overflow: hidden;
      max-width: 100%;
      &:not(:last-child) {
        border-bottom: 1px solid $component-border;
      }
      &:hover {
        background-color: $snw-secondary-background;
        cursor: pointer;
      }
    }
  }
  .snw-noResults {
    display: none;
    color: $snw-secondary-color;
    text-align: center;
    top: 0;
    position: relative;
    padding: $snw-main-padding;
    background-color: $snw-component-background;
    .snw-icon-info {
      &:before {
        font-family: 'snw-material-icons';
        content: '\e88f';
        font-size: 77px;
      }
    }
  }
}

&[max-width='720px'] {
  .snw-travel-news-panel {
    position: fixed !important;
    margin: 0;
    width: 100%;
    font-size: 16px;
    -webkit-text-size-adjust: none; // Fixes DE335
    max-height: calc(100% - 78px); // FIXES DE112
    .snw-travel-news-list {
      width: 100%;
      max-height: calc(100% - #{$snw-button-see-map-height});
    }
    .snw-noResults {
      width: 100%;
      min-height: 170px;
    }
  }
}