.SearchForm {
  margin: 0 10px;
}

.snw-full-width {
  width: 100%;
  box-sizing: border-box;
}

.snw-form-group {
  padding-top: 5px;
  padding-bottom: 5px;
  margin-bottom: 0;
  .snw-control {
    display: block;
    width: 100%;
    height: 34px;
    margin-bottom: 10px;
    box-sizing: border-box;
  }
  .snw-btn {
    margin-bottom: 10px;
  }
  .snw-control-label {
    font-size: 14px;
    color: #4d4d4d;
    font-weight: bold;
    font-family: Arial;
  }
}

.snw-btn {
  display: inline-block;
  padding: 6px 12px;
  font-size: 14px;
  color: white;
  border: 1px solid transparent;
  background-color: #818A8F;
  &:hover {
    background-color: #687075;
  }
}

.snw-select {
  background-color: white;
}