.snw-ts-iw-title {
  font-weight: bold;
  font-size: 14px;
}

.snw-ts-iw-content {
  font-size: 12px;
  margin-top: 5px;
  table {
    width: 100%;
  }
  .snw-data-item {
    width: 100%;
    td {
      width: 35%;
    }
  }
  .snw-data-value {
    display: flex;
    align-items: center;
    padding: 2px;
    width: 100% !important;
    .line {
      margin-right: 5px;
    }
    img {
      height: 20px;
      width: 20px;
    }
  }
}