//icons

.snw-clickable {
  color: $icon-clickable;
  cursor: pointer;

  &:hover {
    color: $icon-clickable-hover;
  }
}
//datepicker
@media (max-width:$snw-break-mobile) {
  #ui-datepicker-div {
    width: 60%;
  }
}
@media (max-width:$snw-break-320) {
  #ui-datepicker-div {
    width: 70%;
  }
}

#ui-datepicker-div {
  display: none;
}

.snw-datepicker-popup {
  &.ui-datepicker {
    background-color: white;
    border: 1px solid #ddd;
    -webkit-box-shadow: 0 5px 10px rgba(0,0,0,0.2);
    -moz-box-shadow: 0 5px 10px rgba(0,0,0,0.2);
    box-shadow: 0 5px 10px rgba(0,0,0,0.2);
    font-family: snw-roboto;
    padding-top: 15px;
    cursor: default;
  }

  .ui-datepicker-header {
    border: none;
    color: $snw-datepicker-primary-color;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: bold;
    margin-bottom: 10px;
    height: 17px;

    .ui-datepicker-prev {
      float: left;
      height: 100%;
    }

    .ui-datepicker-next {
      float: right;
      height: 100%;
    }

    .ui-datepicker-title {
      text-align: center;
    }
  }

  .ui-datepicker-month::after {
    content: ", ";
  }

  .ui-datepicker-calendar td,
  .ui-datepicker-calendar th {
    text-align: center;
    height: 30px;
    width: 30px;
    line-height: 30px;
    color: gray;
    font-weight: 300;
    font-size: 12px;

    &.ui-state-disabled {
      cursor: default;
      opacity: 0.4;
    }
  }

  .ui-state-default,
  .ui-widget-content .ui-state-default {
    display: block;
    border: none;
    font-size: 12px;
  }

  .ui-state-default {
    text-decoration: none;
    color: #666;

    &:hover {
      text-decoration: none;
      background-color: transparent;
    }
  }

  .ui-state-highlight,
  .ui-widget-content .ui-state-highlight {
    background-color: $snw-datepicker-hoverDay-color;
    font-weight: bold;
    color: #fff;
  }

  .ui-datepicker-today {
    .ui-state-default {
      background-color: $snw-datepicker-hoverDay-color;
      color: $snw-datepicker-selectedDay-color;
      font-weight: bold;
    }
  }

  .ui-datepicker-current-day {
    .ui-state-default {
      background-color: $snw-datepicker-selectedDay-color;
      color: #fff;
    }

    .ui-state-active {
      color: #fff;
    }
  }

  .ui-icon {
    @extend .snw-icon;
  }

  .ui-icon-circle-triangle-e,
  .ui-icon-circle-triangle-w {
    color: transparent;
    width: 20px;
    cursor: pointer;
    display: inline-block;
    height: 100%;
    @extend .snw-icon-chevron-left;

    &:before {
      font-weight: bold;
      font-size: 20px;
      color: $snw-datepicker-selectedDay-color;
      position: relative;
      top: -2px;
    }
  }

  .ui-icon-circle-triangle-e {
    @extend .snw-icon-chevron-right;
  }

  .ui-icon-circle-triangle-w {
    @extend .snw-icon-chevron-left;
  }
}
//timepicker

.ui-timepicker-wrapper {
  overflow-y: auto;
  height: 150px;
  width: 6.5em;
  background: #fff;
  border: 1px solid #ddd;
  -webkit-box-shadow: 0 5px 10px rgba(0,0,0,0.2);
  -moz-box-shadow: 0 5px 10px rgba(0,0,0,0.2);
  box-shadow: 0 5px 10px rgba(0,0,0,0.2);
  outline: none;
  z-index: 10001;
  margin: 0;
  font-size: 12px;
  font-family: roboto;
}

.ui-timepicker-wrapper.ui-timepicker-with-duration {
  width: 13em;
}

.ui-timepicker-wrapper.ui-timepicker-with-duration.ui-timepicker-step-30,
.ui-timepicker-wrapper.ui-timepicker-with-duration.ui-timepicker-step-60 {
  width: 11em;
}

.ui-timepicker-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.ui-timepicker-duration {
  margin-left: 5px;
  color: #888;
}

.ui-timepicker-list:hover .ui-timepicker-duration {
  color: #888;
}

.ui-timepicker-list li {
  padding: 3px 0 3px 5px;
  cursor: pointer;
  white-space: nowrap;
  color: #666;
  list-style: none;
  margin: 0;
}

.ui-timepicker-list:hover .ui-timepicker-selected {
  background: #fff;
  color: #000;
}

.ui-timepicker-list .ui-timepicker-selected:hover,
.ui-timepicker-list li:hover,
li.ui-timepicker-selected {
  background: #1980EC;
  color: #fff;
}

.ui-timepicker-list li:hover .ui-timepicker-duration,
li.ui-timepicker-selected .ui-timepicker-duration {
  color: #ccc;
}

.ui-timepicker-list li.ui-timepicker-disabled,
.ui-timepicker-list li.ui-timepicker-disabled:hover,
.ui-timepicker-list li.ui-timepicker-selected.ui-timepicker-disabled {
  color: #888;
  cursor: default;
}

.ui-timepicker-list li.ui-timepicker-disabled:hover,
.ui-timepicker-list li.ui-timepicker-selected.ui-timepicker-disabled {
  background: #f2f2f2;
}
