.snw-header {
  .snw-icon-following {
    &:before {
      content: '\e7f7';
    }
  }
}

.snw-followed-routes {
  .snw-content {
    .snw-route {
      padding: $snw-main-padding 5px;
      display: flex;
      cursor: default;
      * {
        cursor: default;
      }
      &.snw-selectable * {
        cursor: pointer;
      }
      .snw-icon-route {
        background-image: url(../images/icons1x/swn-rdg-icon-check-journey-grey-22-x-26.png);
        width: 30px;
        height: 30px;
        background-size: contain;
      }
      .snw-route-content {
        width: calc(100% - 30px);
        .snw-origin,
        .snw-destination {
          display: flex;
        }
        .snw-location-name {
          width: 60%;
          @include truncate();
        }
        .snw-scheduled {
          width: 20%;
          &.snw-changed-scheduled {
            text-decoration: line-through;
          }
        }
        .snw-estimated {
          width: 20%;
        }
      }
    }
  }
}