.ui-menu-item {
  i {
    width: 30px;
    padding: 5px 20px;
    &:before {
      margin: 0px;
    }
  }
  i.autocomplete-ui-icon+span {
    padding: 10px !important;
  }
  .ui-state-active {
    background-color: #E6E6E6;
  }
  display: flex;
  padding: 0px !important;
}

.ui-autocomplete.ui-front.ui-menu.ui-widget.ui-widget-content {
  position: relative;
  width: auto !important;
  li {
    i.autocomplete-ui-icon {
      &.default:before {
        background-image: url('../images/googleplaces_default.png')!important;
      }
    }
  }
}
.snw-autocomplete {
  .search-section {
    input {
      font-size: 0.9em;
    }
    .icon-ic_close_black_24px {
      font-family: 'snw-material-icons';
      font-style: normal;
      border-left: none !important;
      &:before {
        content: "\e14c";
      }
    }

    .icon-ic_search_black_24px {
      font-family: 'snw-material-icons';
      font-style: normal;
      border-left: none !important;
      padding-left: 8px;
      &:before {
        content: "\e8b6";
      }
    }
    .icon-ic_close_black_24px {
      font-family: 'snw-material-icons';
      font-style: normal;
      border-left: none !important;
      padding-left: 8px;
    }
  }
}
