@mixin component-button() {
  color: $snw-darker-gray;
  height: $snw-btn-size;
  width: $snw-btn-size;
  border: $snw-btn-border;
  cursor: pointer;
  display: inline-block;
  background-color: white;
}

@mixin active-component() {
  background-color: #fff;
  color: hsl(0, 0%, 70%);
}

@mixin category-btn($backcolor: #fff, $color: grey) {
  color: $color;
  float: left;
  font-size: 20px;
  background-color: $backcolor;
  width: 35px;
  height: 100%;
  .icon-container {
    width: 100%;
    height: 100%;
    @include center-element();
  }
}

@mixin non-selectable() {
  -moz-user-select: none;
  /* Firefox */
  -ms-user-select: none;
  /* Internet Explorer */
  -khtml-user-select: none;
  /* KHTML browsers (e.g. Konqueror) */
  -webkit-user-select: none;
  /* Chrome, Safari, and Opera */
  -webkit-touch-callout: none;
  /* Disable Android and iOS callouts*/
}

@mixin truncate() {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin truncate-multiline($lineHeight: 1.2em, $lineCount: 2, $textAlign: justify) {
  overflow: hidden;
  position: relative;
  line-height: $lineHeight;
  max-height: $lineHeight * $lineCount;
  text-align: $textAlign;
  margin-right: -1em;
  padding-right: 0.9em;
  &:before {
    content: '...';
    position: absolute;
    right: 0;
    bottom: 0;
  }
  &:after {
    content: '';
    position: absolute;
    right: 0;
    width: 1em;
    height: 1em;
    margin-top: 0.2em;
    background: #fff;
  }
}

@mixin center-element($content-align: center) {
  display: flex;
  align-items: center;
  justify-content: $content-align;
}

@mixin triangle() {
  width: 0;
  height: 0;
  position: absolute;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
}

@mixin box-shadow($top: 1px, $left: 1px, $blur: 1px, $spread: 0, $color: $snw-light-gray, $inset: false) {
  @if $inset {
    -webkit-box-shadow: inset $top $left $blur $spread $color;
    -moz-box-shadow: inset $top $left $blur $spread $color;
    box-shadow: inset $top $left $blur $spread $color;
  }
  @else {
    -webkit-box-shadow: $top $left $blur $spread $color;
    -moz-box-shadow: $top $left $blur $spread $color;
    box-shadow: $top $left $blur $spread $color;
  }
}

@mixin component-box-shadow($size: $snw-component-shadow-size, $color: $snw-component-shadow-color, $blur: 0) {
  @include box-shadow($size, $size, $size, $blur, $color);
}

@mixin rounded($radius: 0.5em) {
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  border-radius: $radius;
}

@mixin gradient($from, $to, $orientation: left) {
  background: $from;
  /* Old browsers */
  background: -moz-linear-gradient($orientation, $from 0%, $to 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient($orientation, $from 0%, $to 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, 0%, $to 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#{$from}', endColorstr='#{$to}');
}

@function calculateRem($size) {
  $remSize: $size / 16px;
  @return $remSize * 1rem;
}

@mixin font-size($size) {
  font-size: $size;
  font-size: calculateRem($size);
}

@mixin transition($args...) {
  -webkit-transition: $args;
  -moz-transition: $args;
  -ms-transition: $args;
  -o-transition: $args;
  transition: $args;
}

@mixin transform($transforms) {
  -moz-transform: $transforms;
  -o-transform: $transforms;
  -ms-transform: $transforms;
  -webkit-transform: $transforms;
  transform: $transforms;
}

@mixin rotate ($deg) {
  @include transform(rotate(#{$deg}deg));
}

// scale
@mixin scale($scale) {
  @include transform(scale($scale));
}

// translate
@mixin translate ($x, $y) {
  @include transform(translate($x, $y));
}

@mixin opacity($opacity) {
  opacity: $opacity;
  $opacity-ie: $opacity * 100;
  filter: alpha(opacity=$opacity-ie); //IE8
}

@mixin placeholder($color: $snw-component-shadow-color) {
  &::-webkit-input-placeholder {
    /* Chrome, Safari, Opera */
    color: $color;
  }
  &::-moz-placeholder {
    /* Firefox */
    color: $color;
  }
  &:-ms-input-placeholder {
    /* IE10â€“11 */
    color: $color !important;
  }
  &::-ms-input-placeholder {
    /* Edge */
    color: $color;
  }
  &::placeholder {
    /* CSS Working Draft */
    color: $color;
  }
}