.snw-travel-time-box {
  margin-bottom: 10px;
  .snw-list-item {
    @include center-element();
    min-height: 40px;
    padding: 5px 0;
    color: $snw-darker-gray;
    .snw-icon {
      height: 24px;
      width: 24px;
      margin-right: 10px;
      font-size: 24px;
    }
    .snw-text {
      font-size: 12px;
      width: calc(100% - 34px);
      line-height: 1.5em;
    }
  }
  .snw-info-message {
    color: $snw-light-gray;
    .snw-text {
      font-size: 10px;
      color: $snw-gray;
    }
  }
  .snw-name-point-reference {
    @include truncate();
  }
}

.snw-icon-pointRef {
  background-image: url('../images/pointRef.png');
}

.label-hidden {
  display: none;
}

.label-shown {
  display: block;
}

.cluster {
  text-align: center;
}

.snw-geo-marker {
  display: none;
}

.shown {
  display: block;
  position: relative;
  cursor: pointer;
  .snw-marker-label {
    left: calc(50% - 20px);
    top: -18px;
  }
}