.snw-tab-button {
  @include center-element();
  width: 100%;
  background-color: $snw-tab-btn-bg-color;
  color: $snw-tab-btn-color;
  font-size: $snw-tab-btn-font-size;
  height: 100%;
  cursor: pointer;
  &.snw-selected {
    background-color: $snw-tab-btn-bg-color--selected;
    color: $snw-tab-btn-color--selected;
  }
}