.snw-transports-tt {
  margin: $snw-main-menu-padding;
  border: 1px solid #f1f1f1;
  @include rounded($snw-radius);
  @include center-element();
  overflow: hidden;
  .snw-icon {
    font-size: $snw-traveltime-tab-size;
    color: $snw-traveltime-tab-color;
    background-color: $snw-traveltime-tab-bg-color;
    padding: $snw-traveltime-padding;
    &.snw-active,
    &.snw-active:hover {
      color: $snw-traveltime-tab-color--active;
      background-color: $snw-traveltime-tab-bg-color--active;
    }
    &:hover {
      color: $snw-traveltime-tab-color--hover;
      background-color: $snw-traveltime-tab-bg-color--hover;
    }
  }
}

.snw-travel-time-panel-header {
  padding: 0;
  height: $snw-traveltime-header-height;
  line-height: $snw-traveltime-header-height;
  background-color: $snw-traveltime-header-bg-color;
  border-bottom: 1px solid $snw-lighter-gray;
  .snw-travel-time-title {
    font-size: $snw-traveltime-header-size;
    font-weight: $snw-traveltime-header-weight;
    color: $snw-traveltime-header-color;
  }
  .snw-travel-time-checkbox[type="checkbox"] {
    display: none;
  }
  .snw-travel-time-checkbox[type="checkbox"]+ label {
    width: $snw-traveltime-checkbox-size;
    height: $snw-traveltime-checkbox-size;
    border: 2px solid $snw-traveltime-checkbox-bg--checked;
    background-color: $snw-traveltime-checkbox-bg;
    border-radius: $snw-traveltime-checkbox-border-radius;
    background-image: $snw-traveltime-checkbox-bg-image;
    background-size: $snw-traveltime-checkbox-size;
    cursor: pointer;
  }
  .snw-travel-time-checkbox[type="checkbox"]:checked+ label {
    background-color: $snw-traveltime-checkbox-bg--checked;
    background-image: $snw-traveltime-checkbox-bg-image--checked;
  }
}

.snw-travel-time-box {
  margin-bottom: 10px;
  .snw-list-item {
    @include center-element();
    min-height: 40px;
    padding: 5px 0;
    color: $snw-darker-gray;
    .snw-icon {
      height: 24px;
      width: 24px;
      margin-right: 10px;
      font-size: 24px;
    }
    .snw-text {
      font-size: 12px;
      width: calc(100% - 34px);
      line-height: 1.5em;
    }
  }
  .snw-info-message {
    color: $snw-light-gray;
    .snw-text {
      font-size: 10px;
      color: $snw-gray;
    }
  }
  .snw-name-point-reference {
    @include truncate();
  }
}