.snw-label {
  background-color: $snw-label-background;
  padding: 5px;
  white-space: nowrap;
  box-shadow: $snw-box-shadow;
  &.label-down-left {
    margin-top: -6px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      left: 4px;
      margin-top: 22px;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-top: 9px solid $snw-label-background;
    }
  }
  &.label-down-right {
    margin-top: -6px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      right: 4px;
      margin-top: 22px;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-top: 9px solid $snw-label-background;
    }
  }
  &.label-up-left {
    margin-top: 6px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      left: 4px;
      margin-top: -2px;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 9px solid $snw-label-background;
    }
  }
  &.label-up-right {
    margin-top: 6px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      right: 4px;
      margin-top: -2px;
      border-left: 6px solid transparent;
      border-right: 6px solid transparent;
      border-bottom: 9px solid $snw-label-background;
    }
  }
  &.label-left {
    margin-left: 4px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      position: absolute;
      left: 0;
      margin-top: 9px;
      margin-left: -4px;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-right: 9px solid $snw-label-background;
    }
  }
  &.label-right {
    margin-right: 4px;
    &::after {
      content: '';
      width: 0;
      height: 0;
      right: -3px;
      position: absolute;
      margin-top: 9px;
      border-top: 6px solid transparent;
      border-bottom: 6px solid transparent;
      border-left: 9px solid #fff;
    }
  }
  .transport-icon {
    width: 15px;
    height: 18px;
    top: 3px;
    position: relative;
    &:before {
      color: $snw-font-color;
      font-size: $snw-label-font-size;
      vertical-align: top;
      top: 5px;
      position: relative;
    }
  }
  .arrow-right {
    height: 10px;
  }
  .snw-label-duration {
    color: $snw-font-color;
    font-size: $snw-label-font-size;
    font-weight: normal;
    vertical-align: middle;
  }
}