.snw-service-details {
  margin: 0;
  font-size: $snw-tabs-font-size;
  padding: $snw-main-padding;
  border-bottom: 1px solid #ddd;
  .snw-share {
    cursor: pointer;
    .snw-md-icon-share {
      // position: relative;
      // left: 8px;
      display: block;
      color: #32A8E0;
    }
    & > span {
      color: #999;
    }
  }
  .snw-md-icon-star-empty {
    cursor: pointer;
    float: right
  }
  .snw-service-time-info {
    padding: $snw-main-padding;
    line-height: 20px;
    height: 40px;
    display: inline-block;
    width: 100%;
    padding-right: 0;
    .snw-service-time-container {
      display: inline-block;
      max-width: 70%;
      span {
        display: inline-block;
      }
      span.snw-service-time-scheduled {
        text-decoration: line-through;
        padding-right: 1em;
      }
    }
    .snw-icon-transit {
      vertical-align: bottom;
      color: $snw-icon-color;
    }
    .snw-service-date {
      display: none;
      padding-left: $snw-main-margin;
    }
    .snw-cta-back-panel {
      display: none;
      float: right;
      cursor: pointer;
      cursor: hand;
      .snw-icon-chevron-right {
        margin-bottom: -5px;
      }
    }
    .snw-delayed-time-collapse {
      display: none;
    }
    .snw-service-duration-cell {
      .snw-service-duration-scheduled {
        margin-right: $snw-main-margin;
        text-decoration: line-through;
      }
    }
    .snw-route-info-summary-detail {
      display: block;
      padding-left: 25px;
      position: relative;
      bottom: 23px;
      .snw-route-info-summary-text {
        display: inline-block;
        max-width: 70%;
        padding-right: 5px;
        .snw-info-separator {
          color: #D0D0CF;
        }
        .snw-route-from-to {
          display: inline-block;
          vertical-align: top;
        }
      }
      .snw-service-duration {
        padding-right: 20px;
      }
    }
  }
  .snw-follow-route {
    color: $snw-icon-color;
    cursor: pointer;
    &:before {
      content: '\e7f4';
    }
    &.isFollowed {
      &:before {
        content: '\e7f7';
      }
    }
  }
  .snw-share,
  .snw-service-duration-cell,
  .snw-follow-route {
    float: right;
    position: relative;
    bottom: 2px;
  }
  .snw-share,
  .snw-follow-route {
      font-size: 25px;
  }
  .snw-share,
  .snw-service-duration-cell {
    padding: 2px 10px 0px 0px;
  }
  .snw-service-station-info {
    padding: $snw-main-padding $snw-main-padding $snw-main-padding 25px;
    line-height: 30px;
    display: table;
    .snw-service-station-row {
      display: table-row;
      .swn-service-station-info-content {
        display: table-cell;
      }
      .swn-service-station-info-btn-see-map {
        display: none;
      }
    }
    .snw-icon-walking {
      vertical-align: sub;
    }
    .snw-info-separator {
      vertical-align: middle;
      color: #D0D0CF;
    }
    .snw-transbord-icon {
      width: $snw-icon-size;
      height: $snw-icon-size;
      display: inline-block;
      background-repeat: no-repeat;
      background-size: contain;
      width: 20px;
      height: 10px;
      padding: $snw-main-padding;
      vertical-align: middle;
      &.snw-icon-train {
        background-image: url('../images/icons2x/snw-rdg-icon-railway-red.png');
      }
      &.snw-icon-bus,
      &.snw-icon-replacement_bus,
      &.snw-icon-scheduled_bus {
        background-image: url('../images/icons2x/bus.png');
      }
      &.snw-icon-walk {
        background-image: url('../images/icons2x/walk.png');
      }
      &.snw-icon-underground,
      &.snw-icon-subway,
      &.snw-icon-metro {
        background-image: url('../images/icons2x/underground.png');
      }
      &.snw-icon-tramlink {
        background-image: url('../images/icons2x/tramlink.png');
      }
      &.snw-icon-ferry {
        background-image: url('../images/ferry.png');
      }
      &.snw-icon-transfer {
        background-image: url('../images/icons2x/transit-transfer.png');
      }
      &.snw-icon-overground {
        background-image: url('../images/snw-step-overground.svg');
      }
    }
    .snw-company {
      white-space: nowrap;
    }
    .snw-service-company {
      padding: 5px;
      background-color: $snw-company-background;
      color: $snw-company-color;
    }
    .snw-travel-mode-icon {
      .snw-icon-bicycling,
      .snw-icon-driving {
        position: relative;
        top: 4px;
      }
    }
  }
  .snw-service-delayed {
    padding: $snw-main-padding $snw-main-padding $snw-main-padding 25px;
    display: table;
    width: 100%;
    .snw-service-delayed-row {
      display: table-row;
      width: 100%;
      .snw-cell-right {
        text-align: right;
      }
      .snw-service-delayed-cell {
        display: table-cell;
        width: 50%;
      }
    }
    .snw-cta-see-map {
      display: none;
      padding: 10px;
      width: 85px;
      position: relative;
      top: 12px;
      float: right;
      cursor: pointer;
      cursor: hand;
      border: 1px solid;
      border-radius: 10%;
      text-align: center;
    }
    .snw-details {
      display: none;
      .snw-icon-chevron-right {
        margin-bottom: -5px;
      }
    }
  }
  .snw-cta-see-map {
    display: none;
    padding: 10px;
    width: 85px;
    float: right;
    cursor: pointer;
    cursor: hand;
    border: 1px solid;
    border-radius: 10%;
    text-align: center;
  }
  .snw-cta-see-map-list-container {
    margin-bottom: 2em;
    .snw-cta-see-map-list-routes {
      padding-right: 10px;
      margin-bottom: 1em;
      display: none;
      padding: 10px;
      width: 85px;
      position: relative;
      top: 12px;
      float: right;
      cursor: pointer;
      cursor: hand;
      border: 1px solid;
      border-radius: 10%;
      text-align: center;
    }
  }
  .snw-service-buttons-cell {
    display: none;
  }
  .snw-service-delayed-msg {
    padding: 10px 10px 10px 25px;
    width: 100%;
    .snw-icon-warning {
      width: 35px;
      font-size: 30px;
      cursor: default;
    }
    .moderateDelay {
      &.snw-icon-warning {
        color: $snw-delayed-color;
      }
    }
    .cancelled,
    .severeDelay {
      &.snw-icon-warning {
        color: $snw-cancelled-color;
      }
    }
    .ontime,
    .minorDelay {
      &.snw-icon-warning {
        color: $snw-ontime-color;
      }
    }
    >* {
      display: table-cell;
      vertical-align: middle;
    }
  }
  .snw-disclaimer {
    display: none;
  }
  .snw-disruption-msg {
    .snw-service-delayed-row {
      span {
        display: table-cell;
        padding-left: 10px;
      }
      i {
        display: table-cell;
        position: relative;
        top: 12px;
      }
    }
  }
}

//.snw-maps-widget-container[max-width='720px'] {
&[max-width='720px'] {
  .snw-service-container-collapse .snw-service-details {
    height: 85px;
    min-height: 85px;
    max-height: 85px;
    display: block;
    position: relative;
  }
  .snw-service-details {
    background-color: #fff;
    .snw-service-time-info {
      padding-right: 0;
      color: #666;
      .snw-icon-transit {
        vertical-align: top;
        padding-right: 10px;
      }
      .snw-service-time-container {
        max-width: 40%;
        display: inline-block;
        .snw-service-time-scheduled {
          padding: 0;
        }
      }
      .snw-share,
      .snw-service-duration-cell,
      .snw-follow-route {
        padding: 2px 10px 0px 0px;
        position: relative;
      }
      .snw-follow-route {
        bottom: 7px;
      }
      .snw-share,
      .snw-follow-route {
          font-size: 30px;
      }
      .snw-service-duration-cell,
      .snw-share {
        position: relative;
        bottom: 2px;
      }
      .snw-delayed-time-collapse {
        .snw-alternative-routes {
          width: 0;
          position: absolute;
          right: 0;
          background-image: url("../images/icons1x/swn-rdg-icon-check-journey-red-22-x-26.png");
          background-color: #fff;
          border-radius: 50%;
          .snw-cta-see-map-label {
            background-color: #E4004F;
            position: absolute;
            top: -24px;
            right: -13px;
            white-space: nowrap;
            padding: 2px;
            font-size: 0.9em;
            border-radius: 4px;
            &:after {
              content: '';
              width: 0;
              height: 0;
              border-right: 3px solid transparent;
              border-top: 4px solid #E4004F;
              border-left: 3px solid transparent;
              bottom: -4px;
              position: absolute;
              right: 25px;
            }
          }
        }
      }
    }
    .snw-service-station-info {
      .snw-icon-train {
        background-image: url('../images/icons1x/snw-rdg-icon-railway-red.png');
      }
      .snw-icon-bus {
        background-image: url('../images/icons1x/bus.png');
      }
      .snw-icon-walk {
        background-image: url('../images/icons1x/walk.png');
      }
      &.snw-icon-underground,
      &.snw-icon-subway,
      &.snw-icon-metro {
        background-image: url('../images/icons1x/underground.png');
      }
      &.snw-icon-tramlink {
        background-image: url('../images/icons1x/tramlink.png');
      }
      .snw-service-station-row {
        display: table-row;
        .swn-service-station-info-content {
          display: table-cell;
          width: 65%;
        }
        .swn-service-station-info-btn-see-map {
          display: table-cell;
          width: 35%;
          vertical-align: middle;
          .snw-cta-see-map {
            padding: 0 12px;
            width: 85px;
            float: right;
            cursor: pointer;
            cursor: hand;
            border: 1px solid;
            border-radius: 8%;
          }
        }
      }
    }
    .snw-service-delayed-cell {
      .snw-cta-see-map {
        display: block;
      }
    }
    .snw-service-buttons-cell {
      display: table;
      width: 100%;
      padding: 10px 10px 10px 25px;
      .snw-alternative-routes {
        float: left;
      }
      .snw-see-map-container {
        float: right;
      }
    }
  }
}

.snw-md-icon-share:before {
  content: "\e80d";
}
