$snw-break-mobile:'720px';
$snw-break-320: '320px';
//margins and paddings
$snw-main-margin: 15px;
$snw-secondary-margin: 10px;
$snw-main-padding: 10px;
//font size
$snw-font-size:17px;
$snw-title-font-size:18px;
$snw-tabs-font-size:14px;
$snw-tabs-small-font-size:13px;
$snw-table-header-font-size:12px;
$snw-label-font-size:14px;
$snw-text-size: 12px;
$snw-header-size:13px;
//colors
$brand-blue: #2FAAE1;
$mid-grey: #676766;
$snw-font-color:#444;
$snw-title-font-color:#fff;
$snw-secondary-color:#B7B9BB;
$snw-component-background:white;
$snw-header-component-background:#999999;
$snw-secondary-background:#f8f8f8;
$snw-hover-background:#999999;
$snw-disclaimer-background: #FEF3EB;
$snw-secondary-hover-background:#efefef;
$snw-hover-color:#fff;
$snw-ontime-color:#52B952;
$snw-little-delayed-color:#5599ff;
$snw-delayed-color:#DA2728;
$snw-cancelled-color:#DA2728;
$snw-label-background:#fff;
$snw-icon-color:#c4c4c4;
$snw-company-background:#e2e2e2;
$snw-company-color:#737272;
$component-border: #dcdcdc;
$icon-default: #fff;
$icon-clickable: #626363;
$icon-clickable-hover: #a2a2a2;
$icon-no-clickable: #a2a2a2;
$icon-active: $brand-blue;
$icon-active-hover: $mid-grey;
$snw-station-blue: #00466E;
$snw-selected-row:#666666;
//Datepicker
$snw-datepicker-primary-color: $snw-blue;
$snw-datepicker-hoverDay-color: $snw-light-blue;
