$snw-box-shadow:1px 1px 0 0 rgba(136, 136, 136, 0.2);
//sizes
$snw-component-width:400px;
$snw-header-component-height:45px;
$snw-icon-size: 18px;
$snw-panel-row-height:37px;
$snw-button-see-map-height:30px;
//Typings
$snw-font-family: snw-roboto;