.snw-incident-tweet{
    padding: 20px;
    padding-bottom: 40px;
    border-bottom: 1px solid #F0F0F0;
    &:not(a) {
        cursor: default;
    }
    .snw-incident-tweet-header {
        .snw-incident-tweet-icon-cell {
            float: left;
            width: 20%;
            text-align: center;
            .snw-tweet-user-icon {
                height: 40px;
                width: 40px;
            }
        }
        .snw-incident-tweet-title {
            float: left;
            width: 70%;
            margin-left: 25px;
            p {
                margin: 0px;
            }
            .snw-incident-tweet-name {
                font-size: 1.4em;
                font-weight: 900;
                cursor: pointer;
            }
            .snw-incident-tweet-username {
                font-size: 1.2em;
                color: darkgrey;
            }
        }
    }
    .snw-incident-tweet-body {
        display: inline-block;
        font-size: 1.4em;
        margin-top: 10px;
    }
    .snw-incident-tweet-footer {
        .snw-incident-tweet-date {
            float: left;
            width: 60%;
            font-size: 1.2em;
            height: 25px;
            line-height: 25px;
        }
        .snw-incident-tweet-options {
            float: left;
            width: 40%;
            height: 25px;
            line-height: 25px;
            text-align: right;
            a {
                margin: 5px;
                display: inline-block;
                width: 25px;
                height: 25px;
                &:hover {
                    opacity: 0.5;
                }
            }

            .snw-tweet-icon-reply {
                background-image: url('../images/snw-tweet-icon-reply.png');
            }

            .snw-tweet-icon-rt {
                background-image: url('../images/snw-tweet-icon-rt.png');
            }

            .snw-tweet-icon-fav{
                background-image: url('../images/snw-tweet-icon-fav.png');
            }
        }
    }
}