.snw-station-container {
  -webkit-box-shadow: $snw-box-shadow;
  -moz-box-shadow: $snw-box-shadow;
  box-shadow: $snw-box-shadow;
  font-size: $snw-font-size;
  color: $snw-font-color;
  margin: 0;
  bottom: 0;
  position: absolute;
  background-color: $snw-component-background;
  height: 100%;
  width: $snw-component-width;
  z-index: 1000010!important;
  cursor: default;
  .snw-station-header {
    height: $snw-header-component-height;
    background-color: $snw-header-component-background;
    color: $snw-title-font-color;
    .snw-station-title {
      cursor: default;
      font-size: $snw-title-font-size;
      padding: $snw-main-padding;
      width: calc(100% - #{$snw-icon-size});
      @include truncate();
    }
    .snw-icon-back,
    .snw-icon-close {
      z-index: 11;
      position: absolute;
      right: 0;
      top: 0;
      padding: $snw-main-padding;
      cursor: pointer;
      width: 40px;
      &:before {
        width: $snw-icon-size;
        height: $snw-icon-size;
      }
    }
    .snw-icon-back {
      left: 0;
      color: $snw-component-background;
      display: none;
    }
  }
  .snw-station-content {
    height: calc(100% - #{$snw-header-component-height});
    overflow-y: auto;
  }
  .snw-cta-station-see-map {
    display: none;
  }
}

@media (max-width:$snw-break-mobile) {
  .snw-station-container {
    width: 100%;
  }
  .snw-station-content {
    height: calc(100% - #{$snw-header-component-height} - 30px) !important;
    overflow-y: auto;
  }
  .snw-cta-station-see-map {
    position: absolute;
    bottom: 0;
    display: inline-block !important;
    height: 35px;
    width: 100%;
    text-align: center;
    padding: 8px;
    font-size: 13px;
    box-shadow: 0px -4px 7px -2px rgba(0,0,0,0.25);
    z-index: 1;
  }
  .snw-station-container-collapse {
    height: 0;
    .snw-station-header {
      .snw-icon-back {
        display: block;
      }
      .snw-station-title {
        margin-left: 30px;
      }
      .snw-close-btn {
        display: none;
      }
    }
  }
}