// List Groups

@mixin list-group-item-variant($state, $background, $color) {
  .snw-list-group-item-#{$state} {
    color: $color;
    background-color: $background;

    // [converter] extracted a&, button& to a.list-group-item-#{$state}, button.list-group-item-#{$state}
  }

  a.snw-list-group-item-#{$state},
  button.snw-list-group-item-#{$state} {
    color: $color;

    .snw-list-group-item-heading {
      color: inherit;
    }

    &:hover,
    &:focus {
      color: $color;
      background-color: darken($background, 5%);
    }
    &.snw-active,
    &.snw-active:hover,
    &.snw-active:focus {
      color: #fff;
      background-color: $color;
      border-color: $color;
    }
  }
}
