.snw-journey-check {
  position: relative;
  margin: 0;
  display: inline-block;
  width: 50%;
  height: 70px;
  background-repeat: no-repeat;
  background-image: url(../images/icons1x/swn-rdg-icon-check-journey-white-22-x-26.png);
  background-position: 10px center;
  text-align: center;
  line-height: 15px;
  font-size: .7em;
  cursor: pointer;
  &.snw-journey-check-active {
    color: $snw-hover-background;
    background-color: #FFF;
    background-image: url(../images/icons1x/swn-rdg-icon-check-journey-grey-22-x-26.png);
  }

  .snw-icon-chevron-down,
  .snw-icon-chevron-up {
    position: absolute;
    top: 35%;
    left: 85%;
  }

  .snw-journey-check-content {
    position: absolute;
    left: 20%;
    top: 40%;
    width: 70%;
    text-align: left;
  }
}

.snw-collapsed .snw-journey-check {
  background-image: url(../images/icons1x/swn-rdg-icon-check-journey-white-22-x-26.png);
}

@media (max-width:$snw-break-mobile) {
  .snw-journey-check {
    height: 48px;
    background-position: 10px center;
    background-size: 19px;
    background-image: url(../images/icons1x/swn-rdg-icon-check-journey-white-22-x-26.png);
    line-height: 15px;
    &.snw-journey-check-active {
      background-image: url(../images/icons1x/swn-rdg-icon-check-journey-grey-22-x-26.png);
      color: $snw-hover-background;
    }

    .snw-journey-check-content {
      position: absolute;
      left: 25%;
      text-align: left;
      margin-left: 5px;
    }

    .snw-icon-chevron-down {
      content: "\e927";
    }    
  }
}