.snw-service-container {
  -webkit-box-shadow: $snw-box-shadow;
  -moz-box-shadow: $snw-box-shadow;
  box-shadow: $snw-box-shadow;
  font-size: $snw-font-size;
  color: $snw-font-color;
  margin: 0;
  bottom: 0;
  position: absolute;
  background-color: $snw-component-background;
  height: 100%;
  width: $snw-component-width;
  z-index: 100000000!important; // So it appears above google's logos even being a map control
  .snw-service-header {
    height: 56px;
    background-color: $snw-header-component-background;
    color: $snw-title-font-color;
    &.snw-service-header--hide-back {
      .snw-icon-back {
        display: none;
      }
      .snw-service-title {
        margin-left: 5px;
      }
    }
    .snw-service-title {
      margin-left: 50px;
      font-size: .8em;
      padding: 7px 30px 7px 7px;
      .snw-service-from,
      .snw-service-to {
        @include truncate();
      }
      .snw-service-from {
        padding-bottom: 4px;
      }
    }
    .snw-icon-back,
    .snw-icon-close {
      z-index: 11;
      position: absolute;
      right: 0;
      top: 0;
      padding: $snw-main-padding;
      cursor: pointer;
      &:before {
        width: $snw-icon-size;
        height: $snw-icon-size;
      }
    }
    .snw-icon-back {
      left: 0;
      color: $snw-title-font-color;
      padding: 17px 10px;
    }
  }
  .snw-service-content {
    height: calc(100% - #{$snw-header-component-height} - 30px);
    display: flex;
    flex-direction: column;
    cursor: default;
  }
  .snw-cta-see-map {
    display: none;
  }
}

@media (max-width:$snw-break-mobile) {
  .snw-service-container {
    width: 100%;
    .snw-service-content {
      overflow-y: auto;
    }
    .snw-service-steps {
      overflow-y: initial;
    }

    .snw-cta-see-map {
      position: absolute;
      bottom: 0;
      display: inline-block;
      height: 35px;
      width: 100%;
      text-align: center;
      padding: 8px;
      font-size: 13px;
      box-shadow: 0px -4px 7px -2px rgba(0,0,0,0.25);
    }
  }
  .snw-service-container-collapse {
    height: 0;
    position: absolute;
    top: 0!important;
    width: 100%;
    .snw-service-content {
      overflow-y: initial;
    }
    .snw-cta-see-map,
    .snw-service-buttons-cell .snw-alternative-routes {
      display: none!important;
    }
    .snw-cta-back-panel {
      display: inline-block!important;
    }
    .snw-service-details {
      background-color: $snw-component-background;
      -webkit-text-size-adjust: 100%!important;
      .snw-service-time-info {
        line-height: inherit;
        padding: 2px 0px 0px 10px;
        .snw-service-time-container {
          margin-bottom: 10px;
          span {
            margin: 0;
          }
        }
        .snw-service-duration,
        .snw-service-time,
        .snw-service-time-scheduled {
          // margin-bottom: 10px;
          vertical-align: top;
          color: #666;
        }
        .snw-icon-transit {
          font-size: 30px;
        }
        .snw-delayed-time-collapse {
          color: $snw-font-color;
          display: block;
          margin: -5px 15px 0px 43px;
          position: relative;
          bottom: -6px;
        }
        .snw-follow-route {
          padding: 0;
        }
        .snw-service-duration-cell,
        .snw-share {
          padding-right: 10px;
        }
      }
      .snw-service-delayed,
      .snw-service-station-info,
      .snw-service-delayed-msg {
        display: none;
      }
    }
  }
}
