.snw-transport-options {
  span {
    font-size: 14px;
    cursor: pointer;

    &:hover {
      color: blue;
    }
  }

  &.snw-disabled {
    span {
      opacity: 0.4;
    }
  }
}

.snw-transport-options-panel {
  display: none;

  .snw-transport-options-header {
    display: flex;

    span {
      font-size: 12px;
      cursor: default;
    }

    .snw-icon-close {
      position: absolute;
      right: 10px;
    }
  }

  .snw-transport-options-selects {
    margin-top: 15px;

    span {
      font-size: 12px;
    }

    .snw-transport-options-driving {
      width: 100%;
    }

    .snw-transport-options-driving,
    .snw-transport-options-transit {
      display: none;

      span {
        font-size: 12px;
        cursor: default;
      }
    }

    .snw-transport-options {
      position: relative;
      width: 50%;

      hr {
        width: 80%;
        margin-top: 0;
        margin-bottom: 15px;
      }
    }

    .snw-option,
    .snw-transport-options-title {
      margin-bottom: 15px;
    }

    .snw-option {
      display: flex;
      align-items: center;
      font-size: 12px;

      label {
        margin-bottom: 0;
        width: 100%;

        span {
          position: relative;
          bottom: 5px;
          cursor: inherit;
        }
      }
    }

    .snw-option-checkbox[type="checkbox"] {
      display: none;
    }

    .snw-option-checkbox[type="checkbox"]+ label,
    .snw-option-checkbox[type="checkbox"]:checked+ label {
      height: 24px;
      cursor: pointer;
      margin-right: 5px;
    }

    .snw-option-checkbox[type="checkbox"]+ label {
      i {
        &:before {
          content: "\e835";
        }
      }
    }

    .snw-option-checkbox[type="checkbox"]:checked+ label {
      i {
        &:before {
          content: "\e834";
        }
      }
    }

    .snw-option-radio[type="radio"] {
      display: none;
    }

    .snw-option-radio[type="radio"]+ label,
    .snw-option-radio[type="radio"]:checked+ label {
      height: 24px;
      cursor: pointer;
      margin-right: 5px;
    }

    .snw-option-radio[type="radio"]+ label {
      i {
        &:before {
          content: "\e836";
        }
      }
    }

    .snw-option-radio[type="radio"]:checked+ label {
      i {
        &:before {
          content: "\e837";
        }
      }
    }
  }
}
