.snw-board {
  margin: 0;
  height: 100%;
  box-shadow: initial;
  .snw-board-table {
    display: table;
    width: 100%;
    .snw-board-tabs {
      display: table-row;
      &>div {
        font-size: $snw-tabs-font-size;
        width: 33.3%;
        padding: 2px;
        height: $snw-panel-row-height;
        cursor: pointer;
        text-align: center;
        display: table-cell;
        vertical-align: middle;
        &:not(.snw-selected) {
          background-color: $snw-secondary-background;
        }
      }
    }
  }
  .snw-board-container {
    position: relative;
  }
}