@mixin columns-fix() {
  // Fixes DE297
  display: table;
  table-layout: fixed;
  tr>* {
    width: 55px;
    &:nth-child(2) {
      // Destination column
      width: auto;
    }
    &:last-child {
      // So cancelled pill fits
      width: 80px;
    }
  }
}

.snw-live-board {
  overflow-x: hidden;
  overflow-y: hidden;
  &.snw-animated {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }
  &>thead {
    cursor: default;
    @include columns-fix();
    width: 100%;
    tr {
      width: 100%;
    }
    th {
      height: $snw-table-header-font-size + 2 + $snw-main-padding * 2;
      padding: $snw-main-padding 5px;
      font-size: $snw-table-header-font-size;
      box-shadow: inset 0px 6px 3px -5px $snw-light-gray, inset 0px -6px 3px -5px $snw-light-gray;
      -webkit-box-shadow: inset 0px 6px 5px -5px $snw-light-gray, inset 0px -6px 5px -5px $snw-light-gray;
      -moz-box-shadow: inset 0px 6px 5px -5px $snw-light-gray, inset 0px -6px 5px -5px $snw-light-gray;
    }
    .snw-countdown {
      background-color: $snw-header-component-background;
      color: $snw-title-font-color;
      font-size: $snw-table-header-font-size;
      text-align: center;
      padding: 5px;
      &:before {
        font-family: 'snw-material-icons';
        content: '\e86a';
        color: $snw-title-font-color;
        vertical-align: middle;
        padding-right: 5px;
      }
    }
  }
  .snw-table {
    height: calc(100% - #{$snw-panel-row-height});
    margin-bottom: 0px;
    table {
      width: 100%;
      height: 100%;
      // @include columns-fix();
      tbody {
        max-height: calc(100% - #{$snw-panel-row-height});
      }
      thead {
        visibility: hidden;
        height: 0;
        display: table-footer-group;
        th {
          padding: 0 5px;
        }
        tr {
          height: 0;
          padding: 0;
        }
      }
    }
    tr {
      height: $snw-panel-row-height;
    }
    th {
      padding: 0;
      font-size: $snw-table-header-font-size;
      height: 0;
      &:last-child {
        width: 30px;
      }
    }
    td {
      padding: $snw-main-padding 5px;
      font-size: $snw-tabs-font-size;
      @media (max-width: $snw-break-mobile) {
        font-size: $snw-tabs-small-font-size;
      }
      &.time {
        color: $snw-secondary-color;
      }
      &.estimated {
        span {
          white-space: nowrap;
        }
      }
      &.cancelled span {
        color: $snw-component-background;
        background-color: $snw-cancelled-color;
      }
      span {
        padding: 5px;
        border-radius: 5px;
      }
    }
    tbody tr {
      cursor: pointer;
      &:nth-child(even) {
        background: $snw-secondary-background;
      }
      &:hover {
        background-color: $snw-hover-background;
        color: $snw-hover-color;
        td,
        .status,
        .icon:before {
          color: $snw-hover-color !important;
        }
      }
    }
  }
  .snw-noResults,
  .snw-serviceError {
    display: none;
    color: $snw-secondary-color;
    text-align: center;
    top: 20%;
    position: relative;
    .snw-icon-info {
      &:before {
        font-family: 'snw-material-icons';
        content: '\e88f';
        font-size: 77px;
      }
    }
  }
}