.snw-panel-tt {
  background-color: $snw-traveltime-bg-color;
  overflow: hidden;
  max-height: 200px;
  margin: -$snw-main-menu-padding;
  .snw-controls {
    height: 45px;
    margin: 12px;
    margin-top: 20px;
    .snw-slider-container {
      @include center-element();
      .snw-limit-value {
        width: 30px;
        text-align: center;
        line-height: 10px;
        &.snw-max {
          margin-left: 5px;
        }
        .snw-value {
          font-weight: 600;
          font-size: 12px;
          color: $snw-traveltime-slider-value;
        }
        .snw-time {
          font-size: 9px;
          color: gray;
        }
      }
    }
    .snw-slider {
      width: calc(100% - 60px);
      position: relative;
      margin: 0 5px;
      height: 5px;
      .ui-slider-handle {
        position: absolute;
        outline: 0;
        z-index: 2;
        cursor: default;
        -ms-touch-action: none;
        touch-action: none;
        top: 0;
        border-radius: 50%;
        cursor: pointer;
      }
      .snw-value-info {
        position: absolute;
        font-size: 10px;
        z-index: 2;
        width: 30px;
        height: 2em;
        cursor: default;
        line-height: 2em;
        text-align: center;
        top: 1.5em;
        font-weight: 700;
        color: $snw-traveltime-slider-label-color;
        background-color: $snw-traveltime-slider-label-bg-color;
        &:after {
          content: '';
          position: absolute;
          bottom: 100%;
          left: calc(50% - 5px);
          border-bottom: 5px solid $snw-traveltime-slider-label-bg-color;
          border-right: 5px solid transparent;
          border-left: 5px solid transparent;
        }
      }
      &.snw-slider-disabled {
        .snw-line {
          display: block;
          border-top: 1px solid $snw-traveltime-slider-disabled;
          width: 100%;
          height: 1px;
          margin-top: 5px;
        }
        .ui-slider-handle {
          border: 1px solid $snw-traveltime-slider-disabled;
          background: $snw-traveltime-slider-disabled-handle;
          width: 10px;
          height: 10px;
        }
        .snw-value-info {
          visibility: hidden;
        }
      }
      &.snw-slider-enabled {
        .snw-line {
          height: 100%;
          background: $snw-traveltime-slider-from-color;
          @include rounded(15px);
          @include gradient($snw-traveltime-slider-from-color, $snw-traveltime-slider-to-color);
        }
        .ui-slider-handle {
          background-color: $snw-traveltime-slider-handle-from-color;
          @include gradient($snw-traveltime-slider-handle-from-color, $snw-traveltime-slider-handle-to-color, top);
          @include component-box-shadow();
          width: 15px;
          height: 15px;
          top: -5px;
        }
      }
    }
    /* support: IE8 - See #6727 */
    .ui-slider.ui-state-disabled .ui-slider-handle,
    .ui-slider.ui-state-disabled .ui-slider-range {
      filter: inherit;
    }
  }
}
